/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState } from 'react';
import * as _ from 'lodash';
import { Dialog, DialogContent } from '@material-ui/core';
import { useForm, Controller } from 'react-hook-form';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { useStylesDatepicker } from 'store/room/constants/http.constant';
import { AppHelper } from 'utils/app.helper';
import CircularProgress from '@material-ui/core/CircularProgress';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';

type Props = {
  open: boolean;
  onChangeExam: any;
  loading: boolean;
  appointmentDate: any;
  onClose: () => void;
};

export function ChangeRoomEndo({
  open,
  onClose,
  loading,
  onChangeExam,
  appointmentDate,
}: Props) {
  const { register, errors, setValue, handleSubmit, control } = useForm();
  const datepickerClass = useStylesDatepicker();
  const [newDate, handleDateChange] = useState(
    !_.isEmpty(appointmentDate)
      ? new Date(appointmentDate)
      : new Date().setDate(new Date().getDate() + 1),
  );

  const [minDate] = useState(new Date().setDate(new Date().getDate() + 1));

  const disableWeekends = date => {
    return date.getDay() === 0 || date.getDay() === 6;
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      disableBackdropClick={true}
      scroll={'body'}
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(onChangeExam)}>
          <h3 className="modal__title px-0">I LCH KHM</h3>
          <div className="w-100 mb-2">
            <div className="form-group text-edit">
              <h5 className="modal__title pl-0">Ngy</h5>
              <Controller
                name="newDate"
                control={control}
                defaultValue={AppHelper.parseBODDate()}
                rules={{ required: true }}
                render={({ field, ...input }: any) => {
                  return (
                    <MuiPickersUtilsProvider
                      utils={DateFnsUtils}
                      locale={viLocale}
                    >
                      <KeyboardDatePicker
                        variant="inline"
                        className={datepickerClass.textField}
                        autoOk={true}
                        placeholder="Nhp ngy thng nm - ex: 01/01/2021"
                        inputVariant="outlined"
                        invalidDateMessage="Vui lng nhp ng nh dng ngy thng"
                        maxDateMessage="Sai nh dng nm hoc nm vt qu gii hn 2100"
                        format="dd/MM/yyyy"
                        margin="normal"
                        minDate={minDate}
                        value={newDate}
                        shouldDisableDate={disableWeekends}
                        onChange={(date: any) => {
                          handleDateChange(date);
                          setValue('newDate', date);
                        }}
                      />
                    </MuiPickersUtilsProvider>
                  );
                }}
              />
            </div>
          </div>
          <div className="w-100 mb-2">
            <h5 className="modal__title pl-0">L do</h5>
            <textarea
              className="form-control"
              rows={3}
              name="reason"
              ref={register({ required: true })}
            />
            {errors.reason && errors.reason?.type === 'required' && (
              <span className="d-block" style={{ color: ' #fb0606' }}>
                Vui lng nhp l do
              </span>
            )}
          </div>
          <div className="modal__btn d-flex justify-content-end mt-4 pr-0">
            <button
              type="button"
              className="btn btn-secondary btn__cancel mr-2"
              onClick={onClose}
            >
              HY
            </button>
            <button
              type="submit"
              className="btn btn-success"
              style={{ minWidth: '100px' }}
            >
              {loading ? <CircularProgress size={20} /> : 'I'}
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
