/* eslint-disable @typescript-eslint/no-unused-vars */
import { createSlice } from 'store/core/@reduxjs/toolkit';
import * as _ from 'lodash';
import { AppHelper } from 'utils/app.helper';
import {
  RESPONSE_CONSTANT,
  RESPONSE_MESSAGE,
} from 'store/queue/constants/queue.constant';
export interface QueueState {
  loading: boolean;
  error: any;
  success: any;
  queues: any;
  queue: any;
  stt: any;
  endoscopic: any;
}

export const initialState: QueueState = {
  loading: false,
  error: null,
  success: {},
  queues: [],
  queue: {},
  stt: {},
  endoscopic: {},
};

const QueueFormSlice = createSlice({
  name: 'queue',
  initialState,
  reducers: {
    getAllQueuePatientRoom(state, action) {
      state.loading = true;
    },

    getAllQueuePatientRoomSuccess(state, action) {
      state.loading = false;
      state.queues = action.payload;
    },

    getAllQueuePatientRoomFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    exportQueuePatientRoom(state, action) {
      state.loading = true;
    },

    exportQueuePatientRoomSuccess(state, action) {
      state.loading = false;
      state.success = action.payload;
    },

    exportQueuePatientRoomFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    removeQueuePatientRoom(state, action) {
      state.loading = true;
    },

    removeQueuePatientRoomSuccess(state, action) {
      state.loading = false;
      state.success = action.payload;
    },

    removeQueuePatientRoomFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getQueueByPatientCode(state, action) {
      state.loading = true;
    },

    getQueueByPatientCodeSuccess(state, action) {
      state.loading = false;
      state.queue = action.payload;
    },

    getQueueByPatientCodeFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getAllSTT(state, action) {
      state.loading = true;
    },

    getAllSTTSuccess(state, action) {
      state.loading = false;
      state.stt = action.payload;
    },

    getAllSTTFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
    },

    getAllEndoscopic(state, action) {
      state.loading = true;
    },

    getAllEndoscopicSuccess(state, action) {
      state.loading = false;
      state.endoscopic = action.payload;
    },

    getAllEndoscopicFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.FIND_PATIENT_FAIL,
        message: action.payload,
      };
    },

    registerSTT(state, action) {
      state.loading = true;
    },

    registerSTTSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_STT_SUCCESS,
        message: RESPONSE_MESSAGE.REGISTER_STT_SUCCESS,
        data: action.payload,
      };
    },

    registerSTTFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_STT_FAIL,
        message: action.payload,
      };
    },

    registerEndoscopic(state, action) {
      state.loading = true;
    },

    registerEndoscopicSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_ENDOSCOPIC_SUCCESS,
        message: RESPONSE_MESSAGE.REGISTER_ENDOSCOPIC_SUCCESS,
        data: action.payload,
      };
    },

    registerEndoscopicFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_ENDOSCOPIC_FAIL,
        message: action.payload,
      };
    },

    registerBoarding(state, action) {
      state.loading = true;
    },

    registerBoardingSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_BOARDING_SUCCESS,
        message: RESPONSE_MESSAGE.REGISTER_BOARDING_SUCCESS,
        data: action.payload,
      };
    },

    registerBoardingFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_BOARDING_FAIL,
        message: action.payload,
      };
    },

    exportSTT(state, action) {
      state.loading = true;
    },

    exportSTTSuccess(state, action) {
      state.loading = false;
      state.success = {
        key: RESPONSE_CONSTANT.EXPORT_STT_SUCCESS,
        message: RESPONSE_MESSAGE.EXPORT_STT_SUCCESS,
        data: action.payload,
      };
    },

    exportSTTFailure(state, action) {
      state.loading = false;
      state.error = {
        key: RESPONSE_CONSTANT.EXPORT_STT_FAIL,
        message: action.payload,
      };
    },

    exportEndoScopic(state, action) {
      state.loading = true;
    },

    exportEndoScopicSuccess(state, action) {
      state.loading = false;
      state.success = {
        key: RESPONSE_CONSTANT.EXPORT_STT_SUCCESS,
        message: RESPONSE_MESSAGE.EXPORT_STT_SUCCESS,
        data: action.payload,
      };
    },

    exportEndoScopicFailure(state, action) {
      state.loading = false;
      state.error = {
        key: RESPONSE_CONSTANT.EXPORT_STT_FAIL,
        message: action.payload,
      };
    },

    reorderNumber(state, action) {
      state.loading = true;
    },

    reorderNumberSuccess(state, action) {
      state.loading = false;
      state.success = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REORDER_NUMBER_SUCCESS,
        message: RESPONSE_MESSAGE.REORDER_NUMBER_SUCCESS,
        data: action.payload,
      };
    },

    reorderNumberFailure(state, action) {
      state.loading = false;
      state.error = {
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REORDER_NUMBER_FAIL,
        message: action.payload,
      };
    },

    setError(state, action) {
      state.error = action.payload;
    },

    setSuccess(state, action) {
      state.success = action.payload;
    },

    clearSuccess(state) {
      state.success = {};
    },

    clearError(state) {
      state.error = {};
    },

    resetAll(state) {
      state.error = null;
      state.success = {};
      state.queues = [];
      state.queue = {};
      state.stt = {};
      state.endoscopic = {};
    },
  },
});

export const { actions, reducer, name: sliceKey } = QueueFormSlice;
