/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import * as LocationSelectors from 'store/location/shared/selectors';
import * as QueueSelectors from 'store/queue/shared/selectors';
import * as QueueSlice from 'store/queue/shared/slice';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import * as _ from 'lodash';
import { useSelector, useDispatch } from 'react-redux';
import { QueueSaga } from 'store/queue/shared/saga';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { RESPONSE_CONSTANT } from 'store/queue/constants/queue.constant';
import { PatientPriorityTypes } from 'store/patient/constants/patient.constant';
import { DEFAULT_FORMAT_INPUT_DATE } from 'store/common/constants/common.constant';
import { AppHelper } from 'utils/app.helper';
import { makeStyles } from '@material-ui/core';
import { useForm } from 'react-hook-form';
import AppLoading from 'app/components/Loading';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DateFnsUtils from '@date-io/date-fns';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

const useStyles = makeStyles({
  root: {
    '& legend': {
      fontSize: '14px !important',
      color: '#000',
      fontWeight: 'bold',
    },
  },
});

const defaultUser = {
  hospitalAdmissionCode: '',
  firstName: '',
  lastName: '',
  gender: 0,
  birthDay: '',
  birthdayYearOnly: '',
  groupCode: '',
  provinceId: '',
  distId: '',
  wardId: '',
  street: '',
  phone: '',
  type: '',
  healthInsuranceNumber: '',
};

export function RegisterBoardingModal({ open, onClose }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  useInjectReducer({
    key: QueueSlice.sliceKey,
    reducer: QueueSlice.reducer,
  });
  useInjectSaga({ key: QueueSlice.sliceKey, saga: QueueSaga });
  const registerDay = new Date();
  const locations: any[] = useSelector(LocationSelectors.selectLocations);
  const success: any = useSelector<any>(QueueSelectors.selectSuccess);
  const groups: any = useSelector(DeptGroupSelector.selectGroups);
  const [loading, setLoading] = useState<boolean>(false);
  const { register, errors, handleSubmit, setValue } = useForm();
  const [user, setUser] = useState<any>(defaultUser);
  const [dists, setDists] = useState<any[]>([]);
  const [wards, setWards] = useState<any[]>([]);
  const dateUtil = new DateFnsUtils();

  useEffect(() => {
    if (user.provinceId !== '') {
      const locationSelect = locations.find(
        loc => loc.province_code === user.provinceId,
      );
      if (locationSelect) {
        let dists = _.sortBy(locationSelect.districts, [
          'district_name',
          'district_code',
        ]);
        setDists(dists);
        if (user.distId !== '') {
          const dist = dists.find(dist => dist.district_code === user.distId);
          if (dist) {
            let wards = _.sortBy(dist.wards, ['ward_name', 'ward_code']);
            setWards(wards);
          }
        }
      }
    }
  }, [user]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      switch (success.key) {
        case RESPONSE_CONSTANT.REGISTER_BOARDING_SUCCESS:
          onClose();
          break;
        default:
          break;
      }
    }
  }, [success]);

  const changeProvince = e => {
    setDists([]);
    setWards([]);
    setUser({
      ...user,
      provinceId: e.target.value,
    });
  };

  const changeDist = e => {
    const distId = e.target.value;
    setUser({
      ...user,
      distId,
    });
    const distSelect = dists.find(dist => dist.district_code === distId);
    if (distSelect) {
      let wards = _.sortBy(distSelect.wards, ['ward_name', 'ward_code']);
      setWards(wards);
    }
  };

  const changeWard = e => {
    setUser({
      ...user,
      wardId: e.target.value,
    });
  };

  const changeGroupCode = e => {
    setUser({
      ...user,
      groupCode: e.target.value,
    });
  };

  const changeStreet = e => {
    setUser({
      ...user,
      street: e.target.value,
    });
  };

  const changeBirthDay = e => {
    setUser({
      ...user,
      birthDay: e.target.value,
    });
  };

  const changeFullName = e => {
    setUser({
      ...user,
      fullName: e.target.value,
    });
  };

  const changeGender = event => {
    setUser({
      ...user,
      gender: +event.target.value,
    });
  };

  const changePatientType = e => {
    const _patientType = e.target.value;
    setUser({
      ...user,
      patientType: _patientType,
    });
  };

  const submitRegister = (info: any) => {
    const { firstName, lastName } = AppHelper.getFirstLastName(user.fullName);
    const registerInfo = {
      hospital_admission_code: info.hospitalAdmissionCode,
      group_code: user.groupCode,
      first_name: firstName ? firstName : '',
      last_name: lastName ? lastName : '',
      gender: user.gender,
      birthday: info.birthDay,
      birthday_year_only: false,
      province_id: info.provinceId,
      district_id: info.distId,
      ward_id: info.wardId,
      street: info.street,
      phone: '',
      type: user.patientType,
      health_insurancenumber: null,
    };
    dispatch(QueueSlice.actions.registerBoarding(registerInfo));
  };

  return (
    <Dialog
      disableBackdropClick={true}
      fullWidth={true}
      maxWidth={'md'}
      open={open}
      onClose={onClose}
    >
      <DialogContent>
        {loading && <AppLoading loading={true} />}
        <form onSubmit={handleSubmit(submitRegister)}>
          <h4 className="modal-title mb-4">ng K Ni Tr</h4>
          <div className="row">
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="code">
                M Cp Cu/M Nhp Vin
              </label>
              <div className="input-group">
                <input
                  id="hospitalAdmissionCode"
                  type="text"
                  className="form-control"
                  name="hospitalAdmissionCode"
                  placeholder="Nhp M Cp Cu"
                  ref={register({ required: true })}
                />
              </div>
              {errors.hospitalAdmissionCode &&
                errors.hospitalAdmissionCode?.type === 'required' && (
                  <span style={{ color: ' #fb0606' }}>
                    Vui lng chn m Cp Cu
                  </span>
                )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="control-label text-edit pl-0 font-weight-bold">
                Nhm dch v
              </label>
              <select
                name="groupCode"
                className="form-control"
                value={user.groupCode}
                onChange={changeGroupCode}
                ref={register({ required: true })}
              >
                <option value="">Nhp tn Nhm dch v</option>
                {!_.isEmpty(groups) && groups.data.length
                  ? groups.data.map((row, idx) => (
                      <option value={row.code} key={idx}>
                        {row.description + ' - ' + row.code}
                      </option>
                    ))
                  : []}
              </select>
              {errors.groupCode && errors.groupCode?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn Nhm phng khm
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="fullName">
                Tn bnh nhn
              </label>
              <input
                type="text"
                className="form-control"
                name="fullName"
                placeholder="Nhp Tn bnh nhn"
                onChange={changeFullName}
                ref={register({ required: true })}
              />
              {errors.fullName && errors.fullName?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng nhp h v tn
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="type">
                i tng
              </label>
              <select
                placeholder="i tng"
                autoComplete="off"
                className="form-control w-100"
                name="type"
                value={user.patientType}
                onChange={changePatientType}
                ref={register({
                  required: true,
                })}
              >
                <option value="0">i tng khng u tin</option>
                {PatientPriorityTypes.map(type => (
                  <option key={type.value} value={type.valueType}>
                    {type.label}
                  </option>
                ))}
              </select>
              {errors.type && errors.type?.type === 'required' ? (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn loi i tng u tin
                </span>
              ) : null}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="provinceId">
                Tnh/Thnh ph
              </label>
              <select
                name="provinceId"
                className="form-control"
                value={user.provinceId}
                onChange={changeProvince}
                ref={register({ required: true })}
              >
                {_.isEmpty(user.provinceId) ? (
                  <option value="79">Thnh Ph H Ch Minh</option>
                ) : (
                  <option value="">Tnh/Thnh Ph</option>
                )}
                {locations.map((province, idx) => (
                  <option value={province.province_code} key={idx}>
                    {province.province_name}
                  </option>
                ))}
              </select>
              {errors.provinceId && errors.provinceId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn tnh/thnh ph
                </span>
              )}
            </div>
            <div className="form-group col-sm-6 col-md-3 mb-1 ">
              <label className="font-weight-bold" htmlFor="distId">
                Qun/Huyn
              </label>
              <select
                id="distId"
                name="distId"
                className="form-control"
                value={user.distId}
                onChange={changeDist}
                ref={register({ required: true })}
              >
                {_.isEmpty(user.provinceId) ? (
                  <option value="744">Qun 5</option>
                ) : (
                  <option value="">Chn Qun</option>
                )}

                {dists.length
                  ? dists.map((dist: any) => {
                      return (
                        <option
                          key={dist.district_code}
                          value={dist.district_code}
                        >
                          {dist.district_name}
                        </option>
                      );
                    })
                  : null}
              </select>
              {errors.distId && errors.distId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>Vui lng chn qun</span>
              )}
            </div>
            <div className="form-group col-sm-6 col-md-3 mb-1 ">
              <label className="font-weight-bold" htmlFor="wardId">
                Phng/X
              </label>
              <select
                name="wardId"
                className="form-control"
                placeholder="Phng/X"
                value={user.wardId}
                onChange={changeWard}
                ref={register({ required: true })}
              >
                {_.isEmpty(user.provinceId) ? (
                  <option value="27328">Phng 11</option>
                ) : (
                  <option value="">Chn phng</option>
                )}

                {wards?.length
                  ? wards.map((ward: any) => {
                      return (
                        <option value={ward.ward_code} key={ward.ward_code}>
                          {ward.ward_name}
                        </option>
                      );
                    })
                  : null}
              </select>
              {errors.wardId && errors.wardId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn phng x
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="street">
                ng/Thn/p
              </label>
              <input
                type="text"
                className="form-control"
                name="street"
                placeholder="ng/Thn/p"
                value={user.street}
                onChange={changeStreet}
                ref={register()}
              />
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="birthDay">
                Ngy sinh
              </label>
              <input
                id="birthDay"
                name="birthDay"
                type="date"
                className="form-control"
                placeholder="Ngy sinh"
                ref={register({ required: true })}
                max={dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE)}
                value={
                  !_.isEmpty(user.birthDay)
                    ? dateUtil.format(
                        new Date(user.birthDay),
                        DEFAULT_FORMAT_INPUT_DATE,
                      )
                    : dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE)
                }
                onChange={changeBirthDay}
              />
              {errors.birthDay && errors.birthDay?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn ngy sinh
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 ">
              <label className="font-weight-bold" htmlFor="registerDay">
                Ngy ng k
              </label>
              <input
                id="registerDay"
                name="registerDay"
                type="date"
                className="form-control"
                placeholder="Ngy ng k"
                value={dateUtil.format(
                  new Date(registerDay),
                  DEFAULT_FORMAT_INPUT_DATE,
                )}
                disabled
              />
              {errors.registerDay &&
                errors.registerDay?.type === 'required' && (
                  <span style={{ color: ' #fb0606' }}>
                    Vui lng chn ngy ng k
                  </span>
                )}
            </div>
            <div className="form-group col-sm-12 col-md-6 pt-3 mb-1 ">
              <FormControl component="fieldset" className={classes.root}>
                <FormLabel component="legend">Gii tnh</FormLabel>
                <div>
                  <FormControlLabel
                    control={
                      <Radio
                        checked={!user.gender ? true : false}
                        color="primary"
                        onChange={changeGender}
                        value="0"
                        name="gender"
                      />
                    }
                    label="N"
                  />
                  <FormControlLabel
                    control={
                      <Radio
                        checked={user.gender ? true : false}
                        color="primary"
                        onChange={changeGender}
                        value="1"
                        name="gender"
                      />
                    }
                    label="Nam"
                  />
                </div>
              </FormControl>
            </div>
          </div>
          <div className="form-group d-flex justify-content-end">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              ng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              ng k
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
