/* eslint-disable jsx-a11y/scope */
import React from 'react';

interface Props {
  patients: any[];
  color: string;
  onOrder: any;
}

export function ListPatientComp({ patients, color, onOrder }: Props) {
  return (
    <table className={`table table-sm table-hover table__color${color}`}>
      <thead>
        <tr>
          <th scope="col">STT</th>
          <th scope="col">H V TN</th>
          <th scope="col">TUI</th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        {patients.length
          ? patients.map((item, index) => (
              <tr
                key={index}
                className={item.type ? 'table-danger' : 'table-primary'}
              >
                <td scope="row">{item.queue_number}</td>
                <td>{item.name}</td>
                <td>{item.age}</td>
                <td>
                  <button
                    className="btn btn-primary btn-xs"
                    title="Gi li STT"
                    onClick={() => onOrder(item)}
                  >
                    <i className="fa fa-pencil"></i>
                  </button>
                </td>
              </tr>
            ))
          : null}
      </tbody>
    </table>
  );
}
