/* eslint-disable react-hooks/exhaustive-deps */
import CircularProgress from '@material-ui/core/CircularProgress';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { DisableEachCall } from 'app/components/CallPatient/DisableEachCall';
import { Counter } from 'app/components/Counter';
import * as _ from 'lodash';
import React, { useEffect, useReducer } from 'react';
import { Controller, useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import {
  LocalStorageKey,
  LocalStorageService,
} from 'services/localStorage.service';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { DeptGroupSaga } from 'store/departmentGroup/shared/saga';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import * as DeptGroupSlice from 'store/departmentGroup/shared/slice';
import { CssTextField } from 'store/room/constants/http.constant';

enum ActionType {
  OPENCALL = 'OPENCALL',
  ADD_DEPT = 'ADD_DEPT',
  NUMBER = ' NUMBER',
  SAVE = 'SAVE',
}

export interface NumberCall {
  normal: number;
  priority: number;
}

interface CoorState {
  openCall: boolean;
  deptsCall: any[];
  isSave: boolean;
  number: NumberCall;
}

type CoorAction = {
  type: ActionType;
  payload: any;
};

const initState: CoorState = {
  openCall: false,
  isSave: true,
  deptsCall: [],
  number: {
    normal: 5,
    priority: 5,
  },
};

function reduceState(state: CoorState, action: CoorAction): CoorState {
  const { type, payload } = action;
  switch (type) {
    case ActionType.SAVE:
      return {
        ...state,
        isSave: action.payload,
      };
    case ActionType.OPENCALL:
      return {
        ...state,
        openCall: !state.openCall,
      };
    case ActionType.ADD_DEPT:
      return {
        ...state,
        deptsCall: payload,
      };
    case ActionType.NUMBER:
      return {
        ...state,
        number: action.payload,
      };
    default:
      return state;
  }
}

export function CoordinationCenter() {
  useInjectReducer({
    key: DeptGroupSlice.sliceKey,
    reducer: DeptGroupSlice.reducer,
  });
  useInjectSaga({ key: DeptGroupSlice.sliceKey, saga: DeptGroupSaga });
  const dispatch = useDispatch();
  const localStorage = new LocalStorageService();
  const groups: any = useSelector(DeptGroupSelector.selectGroups);
  const depts: any[] = useSelector(DeptGroupSelector.selectDeptsInGroup);
  const loading: any = useSelector<any>(DeptGroupSelector.selectLoading);
  const { errors, control, setValue, handleSubmit } = useForm();
  const [
    {
      openCall,
      deptsCall,
      isSave,
      number: { normal, priority },
    },
    dispatchCoor,
  ] = useReducer(reduceState, initState);

  useEffect(() => {
    function getCacheDepts() {
      const cacheDepts: any = localStorage.getItem(
        LocalStorageKey.callRoomsList,
      );
      if (!cacheDepts || !cacheDepts.length) return;
      setDeptsAndOpenCall(cacheDepts);
    }
    getCacheDepts();
    dispatch(DeptGroupSlice.actions.getGroups({}));
  }, []);

  const getDeptInGroup = (e, value) => {
    if (!value || _.isEmpty(value)) return;
    setValue('groups', value);
    setValue('depts', null);
    dispatch(DeptGroupSlice.actions.getDeptInGroup(value.code));
  };

  const setDeptsAndOpenCall = (depts: any[]) => {
    if (!depts || !depts.length) return;
    dispatchCoor({
      type: ActionType.ADD_DEPT,
      payload: depts,
    });
    dispatchCoor({
      type: ActionType.OPENCALL,
      payload: undefined,
    });
  };

  const callDepts = ({ depts }: any) => {
    setDeptsAndOpenCall(depts);
    if (isSave) {
      localStorage.setItem({
        key: LocalStorageKey.callRoomsList,
        value: depts,
      });
    }
  };

  return (
    <div className="container-fluid">
      {!openCall ? (
        <div className="limiter1">
          <div className="container-login101 container">
            <form className="row" onSubmit={handleSubmit(callDepts)}>
              <div className="col-md-12 mb-3">
                <div className="form-group text-edit">
                  <label className="text-uppercase mb-2">
                    Chn nhm phng khm
                  </label>
                  <Controller
                    name="groups"
                    control={control}
                    defaultValue={null}
                    rules={{ required: true }}
                    render={({ field, ...input }: any) => {
                      return (
                        <Autocomplete
                          options={
                            _.isEmpty(groups) || !groups.data.length
                              ? []
                              : groups.data
                          }
                          value={input.value}
                          style={{ height: '39px' }}
                          getOptionLabel={(option: any) => option.description}
                          renderInput={(params: any) => {
                            return (
                              <CssTextField
                                style={{
                                  background: 'white',
                                  fontSize: '16px',
                                }}
                                {...params}
                                variant="outlined"
                                name="room"
                                placeholder="Nhp tn nhm phng khm"
                                InputProps={{
                                  ...params.InputProps,
                                  endAdornment: (
                                    <React.Fragment>
                                      {loading && (
                                        <CircularProgress
                                          color="inherit"
                                          size={20}
                                        />
                                      )}
                                      {params.InputProps.endAdornment}
                                    </React.Fragment>
                                  ),
                                }}
                              />
                            );
                          }}
                          onChange={getDeptInGroup}
                        />
                      );
                    }}
                  />
                </div>
                {errors.depts && errors.depts?.type === 'required' && (
                  <span
                    style={{
                      color: ' #fb0606',
                      transform: 'translateY(20px)',
                    }}
                  >
                    Vui lng chn nhm phng khm
                  </span>
                )}
              </div>
              <div className="col-md-12 mb-3">
                <div className="form-group text-edit">
                  <label className="text-uppercase mb-2">Chn phng khm</label>
                  <Controller
                    name="depts"
                    control={control}
                    disablePortal
                    defaultValue={null}
                    rules={{ required: true }}
                    render={({ field, ...input }: any) => {
                      return (
                        <Autocomplete
                          options={
                            _.isEmpty(depts) || !depts.length ? [] : depts
                          }
                          loading={loading}
                          disableCloseOnSelect
                          multiple
                          freeSolo
                          limitTags={1}
                          noOptionsText="Khng tm thy phng khm"
                          value={
                            !input || _.isEmpty(input.value) ? [] : input.value
                          }
                          style={{ minHeight: '39px' }}
                          getOptionLabel={(option: any) => option.name}
                          renderInput={(params: any) => {
                            return (
                              <CssTextField
                                style={{
                                  background: 'white',
                                  fontSize: '16px',
                                }}
                                {...params}
                                variant="outlined"
                                name="room"
                                placeholder="Nhp tn phng khm"
                                InputProps={{
                                  ...params.InputProps,
                                  endAdornment: (
                                    <React.Fragment>
                                      {loading && (
                                        <CircularProgress
                                          color="inherit"
                                          size={20}
                                        />
                                      )}
                                      {params.InputProps.endAdornment}
                                    </React.Fragment>
                                  ),
                                }}
                              />
                            );
                          }}
                          onChange={(e, values) => setValue('depts', values)}
                        />
                      );
                    }}
                  />
                  {errors.depts && errors.depts?.type === 'required' && (
                    <span style={{ color: ' #fb0606' }}>
                      Vui lng chn phng khm
                    </span>
                  )}
                </div>
              </div>
              <div className="wrap-input101 validate-input mb-0">
                <div className="form-check float-right text-edit">
                  <input
                    className="form-check-input mt-1"
                    type="checkbox"
                    checked={isSave}
                    onChange={({ target: { checked } }) =>
                      dispatchCoor({
                        type: ActionType.SAVE,
                        payload: checked,
                      })
                    }
                  />
                  <label
                    className="form-check-label ml-1"
                    style={{ fontWeight: 400 }}
                  >
                    Lu ci t
                  </label>
                </div>
              </div>
              <div className="col-md-12">
                <div className="container-login101-form-btn">
                  <div className="wrap-login101-form-btn">
                    <div className="login101-form-bgbtn"></div>
                    <button
                      className="login101-form-btn"
                      style={{ background: 'red' }}
                    >
                      BT U
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      ) : (
        <>
          <div className="btn-cancel text-right">
            <button
              className="btn btn__default--bg-color"
              onClick={() => {
                dispatchCoor({
                  type: ActionType.OPENCALL,
                  payload: undefined,
                });
                localStorage.removeItem(LocalStorageKey.callRoomsList);
              }}
            >
              kt thc
            </button>
          </div>
          <div className="row justify-content-lg-center">
            {deptsCall.length &&
              deptsCall.map((dept, idx) => (
                <DisableEachCall
                  key={idx}
                  dept={dept}
                  number={{ normal, priority }}
                />
              ))}
          </div>
          <div className="row my-4">
            <div className="col-md-4">
              <p className="font-weight-bold text-uppercase">
                S lng bnh nhn
              </p>
            </div>
            <div className="col-md-4 d-flex align-items-center">
              <p className="room__title mr-3">S THNG</p>
              <Counter
                count={normal}
                isNormal={true}
                setAsc={() =>
                  dispatchCoor({
                    type: ActionType.NUMBER,
                    payload: {
                      priority,
                      normal: normal < 10 ? normal + 1 : 10,
                    },
                  })
                }
                setDesc={() =>
                  dispatchCoor({
                    type: ActionType.NUMBER,
                    payload: {
                      priority,
                      normal: normal > 2 ? normal - 1 : 1,
                    },
                  })
                }
              />
            </div>
            <div className="col-md-4 d-flex align-items-center">
              <p className="room__title mr-3 priority__color">S U TIN</p>
              <Counter
                count={priority}
                isNormal={false}
                setAsc={() =>
                  dispatchCoor({
                    type: ActionType.NUMBER,
                    payload: {
                      normal,
                      priority: priority < 10 ? priority + 1 : 10,
                    },
                  })
                }
                setDesc={() =>
                  dispatchCoor({
                    type: ActionType.NUMBER,
                    payload: {
                      normal,
                      priority: priority > 2 ? priority - 1 : 1,
                    },
                  })
                }
              />
            </div>
          </div>
        </>
      )}
    </div>
  );
}
