/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useRef } from 'react';
import DateFnsUtils from '@date-io/date-fns';
import AppLoading from 'app/components/Loading';
import * as PatientSlice from 'store/patient/shared/slice';
import * as PatientSelectors from 'store/patient/shared/selectors';
import * as LocationSlice from 'store/location/shared/slice';
import * as LocationSelectors from 'store/location/shared/selectors';
import * as _ from 'lodash';
import { PatientPriorityTypes } from 'store/patient/constants/patient.constant';
import { PatientSaga } from 'store/patient/shared/saga';
import { LocationSaga } from 'store/location/shared/saga';
import { PatientHttp } from 'store/patient/services/patient.http';
import { QueueHttp } from 'store/queue/services/queue.http';
import { APP_CONSTANT } from 'store/common/constants/common.constant';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useDispatch, useSelector } from 'react-redux';
import { DEFAULT_FORMAT_INPUT_DATE } from 'store/common/constants/common.constant';
import { AppHelper } from 'utils/app.helper';
import { PrintPriorityPatient } from '../components/PrintPriorityPatient';
import { useReactToPrint } from 'react-to-print';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import {
  RESPONSE_CONSTANT,
  RESPONSE_MESSAGE,
} from 'store/patient/constants/patient.constant';
import { useForm, Controller } from 'react-hook-form';
import { configResponse } from 'utils/request';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

class PatientModel {
  code: string = '';
  wardId: string = '';
  provinceId: string = '';
  distId: string = '';
  wardName: string = '';
  provinceName: string = '';
  distName: string = '';
  gender: string = '';
  firstName: string = '';
  lastName: string = '';
  fullName: string = '';
  birthDay: string = '';
  street: string = '';
  constructor(userRes: any) {
    this.code = userRes.code ? userRes.code : '';
    this.wardId = userRes.ward_id ? userRes.ward_id : userRes.ward_code;
    this.wardName = userRes.ward_name;
    this.provinceId = userRes.province_id
      ? userRes.province_id
      : userRes.province_code;
    this.provinceName = userRes.province_name;
    this.distId = userRes.district_id
      ? userRes.district_id
      : userRes.district_code;
    this.distName = userRes.district_name;
    this.gender = userRes.gender_name ? userRes.gender : userRes.gender_code;
    this.firstName = userRes.first_name;
    this.lastName = userRes.last_name;
    this.fullName = userRes.full_name;
    this.birthDay = userRes.birthday;
    this.street = userRes.street;
  }
}

const defaultUser = {
  wardId: '',
  provinceId: '',
  distId: '',
  patientType: '',
  name: '',
  gender: '',
  firstName: '',
  lastName: '',
  fullName: '',
  street: '',
  formatBirthDay: '',
};

export function PriorityPatient() {
  useInjectReducer({
    key: PatientSlice.sliceKey,
    reducer: PatientSlice.reducer,
  });
  useInjectSaga({ key: PatientSlice.sliceKey, saga: PatientSaga });
  useInjectReducer({
    key: LocationSlice.sliceKey,
    reducer: LocationSlice.reducer,
  });
  useInjectSaga({ key: LocationSlice.sliceKey, saga: LocationSaga });
  const dateUtil = new DateFnsUtils();
  const webHttp = new PatientHttp(APP_CONSTANT.API.WEB);
  const http = new PatientHttp();
  const queueHttp = new QueueHttp();
  const requestDate: Date = new Date();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const {
    register,
    errors,
    handleSubmit,
    clearErrors,
    setValue,
    control,
    reset,
  } = useForm();
  const notifyController = new NotifyService(enqueueSnackbar);
  const [patientCode, setPatientCode] = useState<string>('');
  const [healthId, setHealthId] = useState<string>('');
  const [queue, setQueue] = useState<any>({});
  const [loading, setLoading] = useState<boolean>(false);
  const [, setOpenPrint] = useState<boolean>(false);
  const error: any = useSelector(PatientSelectors.selectError);
  const success: any = useSelector(PatientSelectors.selectSuccess);
  const locations: any[] = useSelector(LocationSelectors.selectLocations);
  let printRef = useRef<HTMLDivElement>(null);
  const [dists, setDists] = useState<any[]>([]);
  const [wards, setWards] = useState<any[]>([]);
  const [user, setUser] = useState<any>(defaultUser);
  const [userPrint, setUserPrint] = useState<any>({});
  const [disabled, setDisabled] = useState<boolean>(false);
  const [typeLabelPrint, setTypeLablePrint] = useState<any>(null);

  useEffect(() => {
    dispatch(LocationSlice.actions.getLocationsAll());
    return () => {
      PatientSlice.actions.clearError();
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(error)) {
      notifyController.setNotifyMessage(error.message).setWaringNotify();
      dispatch(PatientSlice.actions.clearError());
    }
  }, [error]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      dispatch(PatientSlice.actions.clearSuccess());
    }
  }, [success]);

  useEffect(() => {
    if (user.provinceId !== '') {
      const locationSelect = locations.find(
        loc => loc.province_code === user.provinceId,
      );
      setValue('province_id', locationSelect);
      if (locationSelect) {
        const dists = locationSelect.districts;
        setDists(dists);
        if (user.distId !== '') {
          const dist = dists.find(dist => dist.district_code === user.distId);
          if (dist) {
            setValue('dist_id', dist);
            setWards(dist.wards);
            const initWard = dist.wards.find(
              ward => ward.ward_code === user.wardId,
            );
            if (initWard) {
              setValue('ward_id', initWard);
            }
          }
        }
      }
    }
  }, [user]);

  const reactToPrintContent = React.useCallback(() => {
    return printRef.current;
  }, [printRef.current]);

  const handlePrint = useReactToPrint!({
    content: reactToPrintContent,
    removeAfterPrint: true,
  });

  const changePatienCode = e => setPatientCode(e.target.value);

  const changeHealthId = e => setHealthId(e.target.value);

  const changePatientType = e => {
    const _patientType = e.target.value;
    setUser({
      ...user,
      patientType: _patientType,
    });
    const typeSelect: any = PatientPriorityTypes.find(
      type => type.valueType === _patientType,
    );
    if (typeSelect) {
      setTypeLablePrint(typeSelect);
    }
  };

  const changeProvince = (province: any) => {
    setValue('dist_id', {});
    setValue('ward_id', {});
    if (province) {
      clearErrors('province_id');
      setValue('province_id', province);
    }
    setUser({
      ...user,
      provinceId: province?.province_code,
    });
  };

  const changeDist = dist => {
    setValue('ward_id', {});
    if (dist) {
      setValue('dist_id', dist);
      clearErrors('dist_id');
      const { district_code } = dist;
      const distSelect = dists.find(
        dist => dist.district_code === district_code,
      );
      if (distSelect) {
        setWards(distSelect.wards);
      }
      return setUser({
        ...user,
        distId: district_code,
      });
    }
    setUser({
      ...user,
      distId: '',
    });
  };

  const changeWard = ward => {
    if (ward) {
      clearErrors('ward_id');
    }
    setValue('ward_id', ward);
    setUser({
      ...user,
      wardId: ward ? ward.ward_code : '',
    });
  };

  const changeGender = e => {
    setUser({
      ...user,
      gender: e.target.value,
    });
  };

  const changeName = e => {
    const fullName = e.target.value;
    setUser({
      ...user,
      fullName,
    });
  };

  const changeStreet = e => {
    setUser({
      ...user,
      street: e.target.value,
    });
  };

  const changeBirthDay = e => {
    setUser({
      ...user,
      birthDay: e.target.value,
    });
  };

  const resetState = () => {
    setPatientCode('');
    setHealthId('');
    setDisabled(false);
    setUser(defaultUser);
    setDists([]);
    setWards([]);
    reset();
  };

  const searchWithCode = async (e: any) => {
    e.preventDefault();
    setUser(defaultUser);
    setHealthId('');
    if (patientCode === '') {
      return dispatch(
        PatientSlice.actions.setError({
          id: AppHelper.generateUUID() + Date.now(),
          key: RESPONSE_CONSTANT.KEY_UP_PATIENT_CODE,
          message: RESPONSE_MESSAGE.KEY_UP_PATIENT_CODE,
        }),
      );
    }
    try {
      setLoading(true);
      const res = await http.getPatientWithType({
        patientCode,
        requestDate,
      });
      const data = await configResponse(res);
      await formatUser(data);
      setLoading(false);
    } catch (err) {
      dispatch(
        PatientSlice.actions.setError({
          id: AppHelper.generateUUID() + Date.now(),
          key: RESPONSE_CONSTANT.GET_PATIENT_QR_CODE_FAIL,
          message: _.get(err, 'message'),
        }),
      );
      setLoading(false);
    }
  };

  const searchWithHealthId = async (e: any) => {
    e.preventDefault();
    setUser(defaultUser);
    setPatientCode('');
    if (healthId === '') {
      notifyController
        .setNotifyMessage(RESPONSE_MESSAGE.GET_PATIENT_QR_CODE_FAIL)
        .setFailNotify();
    }
    try {
      setLoading(true);
      const res = await webHttp.getPatientWithQRCode(healthId);
      const data = await configResponse(res);
      await formatUser(data);
      await setLoading(false);
    } catch (err) {
      notifyController.setNotifyMessage(_.get(err, 'message')).setFailNotify();
      setLoading(false);
    }
  };

  const formatUser = userRes => {
    const user = new PatientModel(userRes);
    setUser({
      ...user,
      patientType: '',
    });
  };

  const callError = (error?: string) => {
    const message = error ? error : RESPONSE_MESSAGE.REGISTER_PATIENT_FAIL;
    dispatch(
      PatientSlice.actions.setError({
        id: AppHelper.generateUUID() + Date.now(),
        key: RESPONSE_CONSTANT.REGISTER_PATIENT_FAIL,
        message,
      }),
    );
  };

  const submitRegister = (e: unknown) => {
    if (patientCode !== '') {
      submitPriorityWithCode();
    } else {
      submitPriorityWithQR();
    }
  };

  const submitPriorityWithCode = async () => {
    try {
      setLoading(true);
      const queue = await queueHttp.registerPatientPriority(user);
      const { queue_number } = await configResponse(queue);
      setQueuePrint({
        queue: queue_number,
        user,
      });
    } catch (err) {
      notifyController
        .setNotifyMessage(
          err ? _.get(err, 'message') : RESPONSE_MESSAGE.REGISTER_PATIENT_FAIL,
        )
        .setFailNotify();
      setLoading(false);
    }
  };

  const submitPriorityWithQR = async () => {
    const { firstName, lastName } = AppHelper.getFirstLastName(user.fullName);
    try {
      setLoading(true);
      const resQueue = await webHttp.addTempPatient({
        ...user,
        firstName: firstName ? firstName : '',
        lastName: lastName ? lastName : '',
      });
      const queue = await configResponse(resQueue);
      setQueuePrint({
        queue: _.get(queue, 'data') || {},
        user: queue,
      });
    } catch (err) {
      notifyController
        .setNotifyMessage(
          err ? _.get(err, 'message') : RESPONSE_MESSAGE.REGISTER_PATIENT_FAIL,
        )
        .setFailNotify();
      setLoading(false);
    }
  };

  const setQueuePrint = ({ queue, user }: any) => {
    setQueue(queue);
    setUserPrint(user);
    setOpenPrint(true);
    handlePrint!();
    resetState();
    setLoading(false);
    notifyController
      .setNotifyMessage(RESPONSE_MESSAGE.REGISTER_PATIENT_SUCCESS)
      .setSuccessNotify();
  };

  return (
    <div className="container-fluid">
      {loading && <AppLoading loading={true} />}
      <div className="d-none">
        <PrintPriorityPatient
          patient={userPrint}
          queue={queue}
          type={typeLabelPrint}
          ref={printRef}
        />
      </div>
      <div className="row">
        <div className="col-sm-12 text-center">
          <h2 className="font-weight-bold">THNG TIN BNH NHN</h2>
        </div>
        <div className="col-sm-12">
          <div className="card card-topline-aqua" style={{ height: '100%' }}>
            <div className="card-body">
              <div className="row">
                <form
                  className="col-6"
                  style={{ paddingRight: '30px' }}
                  onSubmit={searchWithHealthId}
                >
                  <div className="form-group d-flex align-items-center">
                    <label
                      className="col-sm-3 control-label text-edit mb-0"
                      style={{ marginRight: '5px' }}
                    >
                      M BHYT
                    </label>
                    <input
                      style={{ width: '50%' }}
                      placeholder="Nhp m BHYT"
                      autoComplete="off"
                      className="form-control"
                      value={healthId}
                      onChange={changeHealthId}
                    />
                    <button
                      className="btn btn-primary btn-sm mb-0 ml-auto"
                      type="submit"
                    >
                      Tm Theo BHYT
                    </button>
                  </div>
                </form>
                <form
                  className="col-6"
                  style={{ paddingRight: '30px' }}
                  onSubmit={searchWithCode}
                >
                  <div className="form-group d-flex align-items-center">
                    <label
                      className="col-sm-3 control-label text-edit mb-0"
                      style={{ marginRight: '5px' }}
                    >
                      M bnh nhn
                    </label>
                    <input
                      style={{ width: '40%' }}
                      placeholder="Nhp M bnh nhn"
                      autoComplete="off"
                      className="form-control"
                      value={patientCode}
                      onChange={changePatienCode}
                    />
                    <button
                      className="btn btn-primary btn-sm mb-0 ml-auto"
                      type="submit"
                    >
                      Tm Theo M bnh nhn
                    </button>
                  </div>
                </form>
                <form
                  className="col-12"
                  onSubmit={handleSubmit(submitRegister)}
                >
                  <div className="row">
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">H v tn</label>
                        <div className="col-sm-9 pl-0">
                          <input
                            type="text"
                            disabled={disabled}
                            placeholder="H v tn"
                            className="form-control"
                            value={user.fullName}
                            name="full_name"
                            onChange={changeName}
                            ref={register({
                              required: !disabled,
                            })}
                          />
                          {errors.full_name &&
                          errors.full_name?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng nhp h v tn
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">Gii tnh</label>
                        <div className="col-sm-9 pl-0">
                          <select
                            name="gender"
                            disabled={disabled}
                            className="form-control"
                            placeholder="Chn gii tnh"
                            value={user.gender}
                            onChange={changeGender}
                            ref={register({
                              required: !disabled,
                            })}
                          >
                            <option value="">Chn gii tnh</option>
                            <option value="1">Nam</option>
                            <option value="0">N</option>
                          </select>
                          {errors.gender &&
                          errors.gender?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng chn gii tnh
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">Tnh/Thnh ph</label>
                        <div className="col-sm-9 pl-0">
                          <Controller
                            control={control}
                            defaultValue={''}
                            name="province_id"
                            rules={{ required: true }}
                            render={({ field, ...input }: any) => {
                              return (
                                <Autocomplete
                                  value={input.value}
                                  noOptionsText="Khng tm thy tnh/thnh ph"
                                  size="small"
                                  getOptionLabel={opt =>
                                    opt.province_name || ''
                                  }
                                  options={
                                    !_.isEmpty(locations) && locations.length
                                      ? locations
                                      : []
                                  }
                                  filterOptions={(options, { inputValue }) => {
                                    return options.filter(option => {
                                      return (
                                        AppHelper.searchVNEntity(
                                          option.province_name,
                                          inputValue,
                                        ) >= 0
                                      );
                                    });
                                  }}
                                  renderInput={params => (
                                    <TextField
                                      {...params}
                                      variant="outlined"
                                      placeholder="Tm tnh / thnh ph"
                                    />
                                  )}
                                  onChange={(e, value) => changeProvince(value)}
                                />
                              );
                            }}
                          />
                          {errors.province_id &&
                          errors.province_id?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng chn tnh/thnh ph
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">Qun/Huyn</label>
                        <div className="col-sm-9 pl-0">
                          <Controller
                            control={control}
                            defaultValue={''}
                            name="dist_id"
                            rules={{ required: true }}
                            render={({ field, ...input }: any) => {
                              return (
                                <Autocomplete
                                  value={input.value}
                                  noOptionsText="Khng tm thy qun/huyn"
                                  size="small"
                                  getOptionLabel={option =>
                                    option.district_name || ''
                                  }
                                  getOptionSelected={option =>
                                    option.district_code
                                  }
                                  options={
                                    !_.isEmpty(dists) && dists.length
                                      ? dists
                                      : []
                                  }
                                  renderInput={params => {
                                    return (
                                      <TextField
                                        {...params}
                                        variant="outlined"
                                        placeholder="Tm qun/huyn"
                                      />
                                    );
                                  }}
                                  onChange={(e, value) => changeDist(value)}
                                />
                              );
                            }}
                          />
                          {errors.dist_id &&
                          errors.dist_id?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng chn qun
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">Phng/X</label>
                        <div className="col-sm-9 pl-0">
                          <Controller
                            control={control}
                            defaultValue={''}
                            name="ward_id"
                            rules={{ required: true }}
                            render={({ field, ...input }: any) => {
                              return (
                                <Autocomplete
                                  value={input.value}
                                  noOptionsText="Khng tm thy phng x"
                                  size="small"
                                  getOptionLabel={option =>
                                    option.ward_name || ''
                                  }
                                  options={
                                    !_.isEmpty(wards) && wards.length
                                      ? wards
                                      : []
                                  }
                                  renderInput={params => (
                                    <TextField
                                      {...params}
                                      variant="outlined"
                                      placeholder="Tm phng x"
                                    />
                                  )}
                                  onChange={(e, value) => changeWard(value)}
                                />
                              );
                            }}
                          />

                          {errors.ward_id &&
                          errors.ward_id?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng chn phng x
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex align-items-center">
                        <label className="col-sm-3 control-label text-edit mb-0">
                          ng/Thn/p
                        </label>
                        <div className="col-sm-9 pl-0">
                          <input
                            placeholder="ng/Thn/p"
                            autoComplete="off"
                            disabled={disabled}
                            className="form-control"
                            name="street"
                            value={user.street}
                            onChange={changeStreet}
                          />
                        </div>
                      </div>
                    </div>
                    <div className="col-6 form-group d-flex">
                      <label className="col-sm-3">i tng</label>
                      <div className="col-sm-9 pl-0">
                        <select
                          placeholder="i tng"
                          autoComplete="off"
                          className="form-control"
                          name="patient_type"
                          value={user.patientType}
                          onChange={changePatientType}
                          ref={register({
                            required: true,
                          })}
                        >
                          <option value="">Chn loi i tng u tin</option>
                          {PatientPriorityTypes.map(type => (
                            <option key={type.value} value={type.valueType}>
                              {type.label}
                            </option>
                          ))}
                        </select>
                        {errors.patient_type &&
                        errors.patient_type?.type === 'required' ? (
                          <span style={{ color: ' #fb0606' }}>
                            Vui lng chn loi i tng u tin
                          </span>
                        ) : null}
                      </div>
                    </div>
                    <div className="col-6">
                      <div className="form-group d-flex">
                        <label className="col-sm-3">Ngy sinh</label>
                        <div className="col-sm-9 pl-0">
                          <input
                            name="birthday"
                            type="date"
                            disabled={disabled}
                            className="form-control"
                            placeholder="Ngy sinh"
                            max={dateUtil.format(
                              new Date(),
                              DEFAULT_FORMAT_INPUT_DATE,
                            )}
                            ref={register({
                              required: !disabled,
                            })}
                            value={
                              !_.isEmpty(user.birthDay)
                                ? dateUtil.format(
                                    new Date(user.birthDay),
                                    DEFAULT_FORMAT_INPUT_DATE,
                                  )
                                : ''
                            }
                            onChange={changeBirthDay}
                          />
                          {errors.birthday &&
                          errors.birthday?.type === 'required' ? (
                            <span style={{ color: ' #fb0606' }}>
                              Vui lng chn ngy sinh
                            </span>
                          ) : null}
                        </div>
                      </div>
                    </div>
                    <div className="col-12 text-center mt-5">
                      <button
                        className="btn btn-danger mr-4 w-25"
                        type="submit"
                      >
                        NG K S th t u Tin
                      </button>
                      <button
                        className="btn btn-primary"
                        type="button"
                        onClick={resetState}
                      >
                        Hu
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
