/* eslint-disable @typescript-eslint/no-unused-expressions */
import React from 'react';
import { TypePatient } from 'store/patient/constants/patient.constant';

export type CountType = 'DESC' | 'ASC';

type Props = {
  config?: any;
  count: number;
  setAsc: () => void;
  setDesc: () => void;
};

interface Config {
  color: string;
  className: string;
}

export function UltraSoundCounter({ config, count, setAsc, setDesc }: Props) {
  const setColorClassName = (): Config | undefined => {
    return {
      className:
        config === TypePatient.NORMAL
          ? 'btn btn__desc btn__default--bg-color'
          : 'btn btn__desc btn-danger',
      color: config === TypePatient.NORMAL ? '' : '#E5252C',
    };
  };

  return (
    <React.Fragment>
      <button className={setColorClassName()?.className} onClick={setDesc}>
        -
      </button>
      <span
        className="number__sreen"
        style={{ color: setColorClassName()?.color }}
      >
        {count}
      </span>
      <button className={setColorClassName()?.className} onClick={setAsc}>
        +
      </button>
    </React.Fragment>
  );
}
