import React from 'react';
import styled from 'styled-components';
import Dialog from '@material-ui/core/Dialog';

const ModalWrap = styled.div<any>`
  background-color: #fff;
  padding: 2em 3em;
  text-align: center;
  border-radius: 0.5em;
  display: block;
`;

const ModalImg = styled.div<any>`
  width: 60px;
  height: 60px;
  margin: 0 auto;
  border-radius: 50%;
  box-shadow: 0 0 0 2px #48db71;
  margin-bottom: 2em;
  display: flex;
`;

const ModalNumber = styled.p<any>`
  font-weight: 600;
  font-size: 2rem;
  margin: auto;
  color: #48db71;
`;

const ModalTitle = styled.div<any>`
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 0.5em;
`;

const ModalText = styled.p<any>`
  margin-bottom: 2em;
  margin-bottom: 2em;
  color: #000;
`;

const ModalButton = styled.button<any>`
  font-size: 1.25em;
  font-weight: bold;
  background-color: #000;
  border: none;
  padding: 0.5em 1em;
  color: #fff;
  box-shadow: 0 0 0 2px #000 inset;
  border-radius: 0.25em;
  cursor: pointer;
  transition: background 0.4s ease, color 0.4s ease;
`;

type Props = {
  open: boolean;
  config: any;
  onClose: () => void;
};

export function ChangeRoomSuccess({ open, config, onClose }: Props) {
  return (
    <Dialog
      maxWidth="sm"
      open={open}
      onClose={onClose}
      disableBackdropClick={true}
    >
      <ModalWrap>
        <ModalTitle>i phng khm thnh cng</ModalTitle>
        <ModalText>
          BN c chuyn qua {config.department_name} vi STT l:{' '}
        </ModalText>
        <ModalImg className="modal-image">
          <ModalNumber>{config.number}</ModalNumber>
        </ModalImg>
        <ModalButton onClick={onClose}>ng</ModalButton>
      </ModalWrap>
    </Dialog>
  );
}
