/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect, useReducer, useMemo, useRef } from 'react';
import * as QueueSelectors from 'store/queue/shared/selectors';
import * as QueueSlice from 'store/queue/shared/slice';
import * as RoomSlice from 'store/room/shared/slice';
import * as LocationSlice from 'store/location/shared/slice';
import * as DeptGroupSlice from 'store/departmentGroup/shared/slice';
import * as AuthSelector from 'store/auth/shared/selectors';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import * as _ from 'lodash';
import { RoomSaga } from 'store/room/shared/saga';
import { QueueSaga } from 'store/queue/shared/saga';
import { LocationSaga } from 'store/location/shared/saga';
import { createStyles, makeStyles, Theme } from '@material-ui/core';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { AppHelper, Order } from 'utils/app.helper';
import { CssTextField } from 'store/room/constants/http.constant';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_INPUT_DATE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {
  RegisterHttp,
  ChangeRoomQueue,
} from 'store/register/services/register.http';
import { configResponse } from 'utils/request';
import { useSnackbar } from 'notistack';
import { NotifyService } from 'services/notify.service';
import { RESPONSE_CONSTANT } from 'store/queue/constants/queue.constant';
import { EMPTY_MESSAGE } from 'store/common/constants/common.constant';
import { RegisterEndoscopicModal } from '../components/RegisterEndoscopicModal';
import { PrintSttEndo } from '../components/PrintSttEndo';
import { useReactToPrint } from 'react-to-print';
import { ChangeRoomEndo } from '../components/ChangeRoomEndo';
import { DeptGroupSaga } from 'store/departmentGroup/shared/saga';
import { RegisterBoardingModal } from '../components/RegisterBoardingModal';
import { useForm } from 'react-hook-form';
import Autocomplete from '@material-ui/lab/Autocomplete';
import AppLoading from 'app/components/Loading';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableHeaderSort from 'app/components/TableHeaderSort';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';

export const colsStt: any = [
  { id: 'no', label: '#', minWidth: 50 },
  { id: 'patient_code', label: 'M cp cu/M BN', minWidth: 150 },
  { id: 'full_name', label: 'H v tn', minWidth: 150 },
  { id: 'birthday', label: 'Ngy sinh', minWidth: 150 },
  { id: 'gender', label: 'Gii tnh', minWidth: 150 },
  {
    id: 'registered_date',
    label: 'Ngy ng k khm',
    minWidth: 200,
  },
  { id: 'department_virtual_name', label: 'Ni thc hin', minWidth: 200 },
  { id: 'type', label: 'Loi Bnh Nhn', minWidth: 170 },
  { id: 'number', label: 'S th t', minWidth: 150 },
  { id: 'appointment_date', label: 'Thi Gian Hn Khm', minWidth: 150 },
  { id: 'actions', label: 'Chc Nng', minWidth: 150 },
];

interface ModalState {
  changeExamModal: boolean;
  registerModal: boolean;
  registerBoardingModal: boolean;
  loadingExam: boolean;
}

enum ModalType {
  OPEN_CHANGE_EXAM = 'OPEN_CHANGE_EXAM',
  OPEN_REGISTER = 'OPEN_REGISTER',
  OPEN_REGISTER_BOARDING = 'OPEN_REGISTER_BOARDING',
  SET_EXAM_LOAD = 'SET_EXAM_LOAD',
}

type ModalActions = {
  type: ModalType;
  payload: any;
};

const initModalState: ModalState = {
  changeExamModal: false,
  registerModal: false,
  registerBoardingModal: false,
  loadingExam: false,
};

const reduceModal = (
  state: ModalState = initModalState,
  { type, payload }: ModalActions,
): ModalState => {
  switch (type) {
    case ModalType.OPEN_CHANGE_EXAM:
      return {
        ...state,
        changeExamModal: payload,
      };
    case ModalType.OPEN_REGISTER:
      return {
        ...state,
        registerModal: payload,
      };
    case ModalType.OPEN_REGISTER_BOARDING:
      return {
        ...state,
        registerBoardingModal: payload,
      };
    case ModalType.SET_EXAM_LOAD:
      return {
        ...state,
        loadingExam: payload,
      };
    default:
      return state;
  }
};

const useStylesDatepicker = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
      maxHeight: '500px',
    },
    textField: {
      width: '100%',
    },
  }),
);

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  table: {
    width: '100%',
    minWidth: '1600px',
  },
  container: {
    height: '100%',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

const defaultPagi = {
  page: 1,
  rowsPerPage: 10,
};

export function ManageEndoscopic() {
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const classes = useStyles();
  const autoClose = useRef<any>(null);
  const dispatch = useDispatch();
  const dateUtil = new DateFnsUtils();
  useInjectReducer({
    key: DeptGroupSlice.sliceKey,
    reducer: DeptGroupSlice.reducer,
  });
  useInjectSaga({ key: DeptGroupSlice.sliceKey, saga: DeptGroupSaga });
  useInjectReducer({ key: RoomSlice.sliceKey, reducer: RoomSlice.reducer });
  useInjectSaga({ key: RoomSlice.sliceKey, saga: RoomSaga });
  useInjectReducer({
    key: LocationSlice.sliceKey,
    reducer: LocationSlice.reducer,
  });
  useInjectSaga({ key: LocationSlice.sliceKey, saga: LocationSaga });
  useInjectReducer({
    key: QueueSlice.sliceKey,
    reducer: QueueSlice.reducer,
  });
  useInjectSaga({ key: QueueSlice.sliceKey, saga: QueueSaga });
  const [
    { changeExamModal, registerModal, registerBoardingModal, loadingExam },
    dispatchModal,
  ] = useReducer(reduceModal, initModalState);
  const regHttp = new RegisterHttp();
  const groups: any = useSelector(DeptGroupSelector.selectGroups);
  const numbersOfEndoscopic: any = useSelector<any>(
    QueueSelectors.selectEndoscopic,
  );
  const loading: any = useSelector<any>(QueueSelectors.selectLoading);
  const success: any = useSelector<any>(QueueSelectors.selectSuccess);
  const error: any = useSelector<any>(QueueSelectors.selectError);
  const [errDate, setErrDate] = useState<any>(false);
  const datepickerClass = useStylesDatepicker();
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('code');
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const user: any = useSelector<any>(AuthSelector.selectUser);
  const [startDate, setStartDate] = useState<Date>(new Date());
  const [endDate, setEndDate] = useState<Date>(new Date());
  const [patientExam, setPatientExam] = useState<any>(null);
  const [printStt, setPrintStt] = useState<any>({});
  const [codeService, setCodeService] = useState<any>(null);
  const { handleSubmit, register, setValue, getValues, control } = useForm();
  let printRef = useRef<HTMLDivElement>(null);
  const reactToPrintContent = React.useCallback(() => {
    return printRef.current;
  }, [printRef.current]);

  const handlePrint = useReactToPrint!({
    content: reactToPrintContent,
    removeAfterPrint: true,
  });

  useEffect(() => {
    dispatch(QueueSlice.actions.getAllEndoscopic({ ...getFormValues() }));
    dispatch(DeptGroupSlice.actions.getGroups({}));
    dispatch(LocationSlice.actions.getLocationsAll());
    return () => {
      dispatch(QueueSlice.actions.resetAll());
    };
  }, []);

  const groupService: any = useMemo((): any => {
    if (!_.isEmpty(groups)) {
      const { data = [] } = groups;
      setCodeService(data);
      return data;
    }
  }, [groups]);

  useEffect(() => {
    if (!_.isEmpty(printStt)) {
      handlePrint!();
    }
  }, [printStt]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      notifyController.setNotifyMessage(success.message).setSuccessNotify();
      if (success.key === RESPONSE_CONSTANT.EXPORT_STT_SUCCESS) {
        AppHelper.createDownloadFile({
          file_path: success.data.file_path,
          file_name: success.data.file_name,
        });
      } else if (success.key === RESPONSE_CONSTANT.REORDER_NUMBER_SUCCESS) {
        dispatch(QueueSlice.actions.getAllEndoscopic({ ...getFormValues() }));
      } else if (
        success.key === RESPONSE_CONSTANT.REGISTER_ENDOSCOPIC_SUCCESS
      ) {
        dispatchModal({
          type: ModalType.OPEN_REGISTER_BOARDING,
          payload: false,
        });
        dispatch(QueueSlice.actions.getAllEndoscopic({ ...getFormValues() }));
        if (success.data) {
          setPrintStt({ ...success.data, status: 'getAllEndoscopic' });
        }
      } else if (success.key === RESPONSE_CONSTANT.REGISTER_BOARDING_SUCCESS) {
        dispatchModal({
          type: ModalType.OPEN_REGISTER_BOARDING,
          payload: false,
        });
        dispatch(QueueSlice.actions.getAllEndoscopic({ ...getFormValues() }));
        if (success.data) {
          setPrintStt(success.data);
        }
      }
      dispatch(QueueSlice.actions.clearSuccess());
    }
  }, [success]);

  useEffect(() => {
    if (!_.isEmpty(error)) {
      notifyController.setNotifyMessage(error.message).setFailNotify();
      dispatch(QueueSlice.actions.clearError());
    }
  }, [error]);

  const getFormValues = (): any => {
    return {
      patientCode: '',
      patientName: '',
      deptCode: !_.isEmpty(codeService) ? codeService.code : '',
      startDate: dateUtil.format(startDate, DEFAULT_FORMAT_INPUT_DATE),
      endDate: dateUtil.format(endDate, DEFAULT_FORMAT_INPUT_DATE),
      page: pagi.page,
      rowsPerPage: pagi.rowsPerPage,
    };
  };

  const handleRequestSort = (event, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const changeStartDate = date => {
    if (date > endDate) {
      setErrDate(true);
      setStartDate(new Date(date));
    } else {
      setErrDate(false);
      setStartDate(new Date(date));
    }
  };

  const changeEndDate = date => {
    if (date < startDate) {
      setErrDate(true);
      setEndDate(new Date(date));
    } else {
      setErrDate(false);
      setEndDate(new Date(date));
    }
  };

  const changeRoom = room => {
    setCodeService(room);
  };
  const submitSearch = (value: any) => {
    if (!errDate) {
      dispatch(
        QueueSlice.actions.getAllEndoscopic({
          ...getFormValues(),
          patientCode: value.patientCode,
          patientName: value.patientName,
        }),
      );
    }
  };

  const validateSttList = () =>
    numbersOfEndoscopic.data && numbersOfEndoscopic.data.length ? true : false;
  const memoSTT: any = useMemo((): any => {
    if (!_.isEmpty(numbersOfEndoscopic)) {
      const { data = [], total_records } = numbersOfEndoscopic;
      return { data, total_records };
    }
  }, [numbersOfEndoscopic]);

  const renderColumns = () => {
    return (
      <TableHeaderSort
        classes={classes}
        onRequestSort={handleRequestSort}
        order={order}
        orderBy={orderBy}
        columns={colsStt}
      />
    );
  };

  const handleChangePage = (event: unknown, page: number) => {
    let changePage = Object.assign({}, pagi, { page: page + 1 });
    setPagi(changePage);
    dispatch(
      QueueSlice.actions.getAllEndoscopic({
        ...getFormValues(),
        page: changePage.page,
      }),
    );
  };

  const handleChangeRowsPerPage = event => {
    let changeRowPerPage = Object.assign({}, pagi, {
      rowsPerPage: +event.target.value,
      page: 1,
    });
    setPagi(changeRowPerPage);
    dispatch(
      QueueSlice.actions.getAllEndoscopic({
        ...getFormValues(),
        rowsPerPage: changeRowPerPage.rowsPerPage,
        page: changeRowPerPage.page,
      }),
    );
  };

  const refreshListStt = () => {
    const close: any = autoClose.current.getElementsByClassName(
      'MuiAutocomplete-clearIndicator',
    )[0];
    if (close) close.click();
    setCodeService(null);
    setPagi(defaultPagi);
    setValue('patientCode', null);
    setValue('patientName', null);
    setStartDate(new Date());
    setEndDate(new Date());
    setErrDate(false);
    const data = {
      deptCode: '',
      patientCode: '',
      patientName: '',
      startDate: dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE),
      endDate: dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE),
      page: 1,
      rowsPerPage: 10,
    };
    dispatch(QueueSlice.actions.getAllEndoscopic(data));
  };

  const exportFile = () => {
    dispatch(
      QueueSlice.actions.exportEndoScopic({
        ...getFormValues(),
        patientCode: getValues('patientCode'),
        patientName: getValues('patientName'),
        employeeId: user.id,
        employeeName: user.full_name,
      }),
    );
  };

  const renderTableControl = () => {
    return (
      <div className="card card-box">
        <div className="card-body">
          <div className="card card-topline-aqua">
            <div className="card-body">
              <form onSubmit={handleSubmit(submitSearch)} className="row">
                <div className="col-sm-12 col-md-6">
                  <div className="form-group d-flex align-items-center">
                    <label className="col-sm-3 control-label text-edit mb-0">
                      M bnh nhn
                    </label>
                    <input
                      type="text"
                      title="M bnh nhn"
                      name="patientCode"
                      placeholder="M bnh nhn"
                      id="patientCode"
                      className="form-control"
                      autoComplete="off"
                      ref={register()}
                    />
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="form-group d-flex align-items-center">
                    <label className="col-sm-3 control-label text-edit mb-0">
                      Tn bnh nhn
                    </label>
                    <input
                      type="text"
                      name="patientName"
                      className="form-control"
                      placeholder="Tn bnh nhn"
                      ref={register()}
                    />
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="form-group d-flex align-items-center">
                    <label className="col-sm-3">Bt u</label>
                    <MuiPickersUtilsProvider
                      utils={DateFnsUtils}
                      locale={viLocale}
                    >
                      <KeyboardDatePicker
                        variant={'inline'}
                        className={datepickerClass.textField}
                        autoOk={true}
                        inputVariant="outlined"
                        orientation="landscape"
                        format="dd/MM/yyyy"
                        margin="normal"
                        value={startDate}
                        onChange={changeStartDate}
                        label={
                          errDate && (
                            <span style={{ color: ' #fb0606' }}>
                              Ngy bt u phi nh hn ngy kt thc
                            </span>
                          )
                        }
                      />
                    </MuiPickersUtilsProvider>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="form-group d-flex align-items-center">
                    <label className="col-sm-3">Kt thc</label>
                    <MuiPickersUtilsProvider
                      utils={DateFnsUtils}
                      locale={viLocale}
                    >
                      <KeyboardDatePicker
                        variant={'inline'}
                        className={datepickerClass.textField}
                        autoOk={true}
                        inputVariant="outlined"
                        orientation="landscape"
                        format="dd/MM/yyyy"
                        margin="normal"
                        value={endDate}
                        onChange={changeEndDate}
                      />
                    </MuiPickersUtilsProvider>
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="form-group d-flex align-items-center">
                    <label className="col-sm-3 control-label text-edit mb-0">
                      Nhm dch v
                    </label>
                    <Autocomplete
                      options={
                        !_.isEmpty(groupService) && groupService.length
                          ? groupService
                          : []
                      }
                      ref={autoClose}
                      style={{ height: '39px', width: '100%' }}
                      getOptionLabel={(option: any) =>
                        option.description + ' - ' + option.code
                      }
                      renderInput={(params: any) => {
                        return (
                          <CssTextField
                            style={{
                              background: 'white',
                              fontSize: '16px',
                            }}
                            {...params}
                            variant="outlined"
                            name="room"
                            placeholder="Nhp tn nhm dch v"
                          />
                        );
                      }}
                      onChange={(event, value) => changeRoom(value)}
                    />
                  </div>
                </div>
                <div className="col-sm-12 col-md-6">
                  <div className="btn-group ml-2">
                    <button
                      className="btn btn-primary"
                      type="submit"
                      disabled={errDate}
                    >
                      Tm kim <i className="fa fa-search"></i>
                    </button>
                  </div>
                  <div className="btn-group ml-2">
                    <button
                      className="btn btn-success"
                      onClick={() => {
                        dispatchModal({
                          type: ModalType.OPEN_REGISTER,
                          payload: true,
                        });
                      }}
                    >
                      ng k <i className="fa fa-plus"></i>
                    </button>
                  </div>
                  <div className="btn-group ml-2">
                    <button
                      className="btn btn-success"
                      type="button"
                      onClick={() => {
                        dispatchModal({
                          type: ModalType.OPEN_REGISTER_BOARDING,
                          payload: true,
                        });
                      }}
                    >
                      ng K Ni Tr<i className="fa fa-plus"></i>
                    </button>
                  </div>
                  <div className="btn-group ml-2">
                    <button
                      className="btn btn-warning"
                      onClick={exportFile}
                      disabled={errDate}
                      type="button"
                    >
                      Xut file <i className="fa fa-file"></i>
                    </button>
                  </div>
                  <div className="btn-group ml-2">
                    <button
                      className="btn btn-info"
                      onClick={refreshListStt}
                      type="button"
                    >
                      Lm mi<i className="fa fa-refresh"></i>
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const reorderNumbers = stt => {
    const data = {
      room_virtual_code: stt.department_virtual_code,
      room_active_code: stt.department_code,
      number: stt.number,
      patient_type: stt.patient_type,
      patient_code: stt.patient_code,
    };
    dispatch(QueueSlice.actions.reorderNumber(data));
  };

  const renderTable = () => {
    if (validateSttList()) {
      const tableData: any[] = AppHelper.stableSort(
        memoSTT.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((stt, idx) => {
        return (
          <TableRow key={stt.id}>
            {colsStt.map(column => {
              const colId = column.id;
              const value = stt[colId];
              if (
                colId === 'created_date' ||
                colId === 'birthday' ||
                colId === 'registered_date' ||
                colId === 'appointment_date'
              ) {
                return (
                  <TableCell key={colId}>
                    {value
                      ? dateUtil.format(new Date(value), DEFAULT_FORMAT_DATE)
                      : '-'}
                  </TableCell>
                );
              }
              if (colId === 'no') {
                const { page, rowsPerPage } = pagi;
                return (
                  <TableCell key={colId}>
                    {page * rowsPerPage + idx + 1}
                  </TableCell>
                );
              }
              if (colId === 'number') {
                return (
                  <TableCell key={colId}>
                    {value && value > 0 ? value : '-'}
                  </TableCell>
                );
              }
              if (colId === 'actions') {
                return (
                  <TableCell key={colId}>
                    {!_.isEmpty(stt.department_code) && (
                      <button
                        className="btn btn-success btn-xs mr-2"
                        title="Gi Li STT"
                        onClick={() => {
                          reorderNumbers(stt);
                        }}
                      >
                        <i className="fa fa-history"></i>
                      </button>
                    )}
                    <button
                      className="btn btn-primary btn-xs"
                      title="Hn Li Lch Khm"
                      onClick={() => {
                        setPatientExam(stt);
                        dispatchModal({
                          type: ModalType.OPEN_CHANGE_EXAM,
                          payload: true,
                        });
                      }}
                    >
                      <i className="fa fa-calendar"></i>
                    </button>
                  </TableCell>
                );
              }
              return <TableCell key={colId}>{value}</TableCell>;
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell className="w-100" align="center" colSpan={colsStt.length}>
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };

  const renderPagination = () => {
    if (validateSttList()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={memoSTT.total_records}
          rowsPerPage={pagi.rowsPerPage}
          page={pagi.page - 1}
          labelRowsPerPage={'Hin Th'}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      );
    }
  };

  const onChangeExam = async ({ newDate, reason }: any) => {
    try {
      dispatchModal({
        type: ModalType.SET_EXAM_LOAD,
        payload: true,
      });
      if (newDate._isAMomentObject) {
        const roomChange: ChangeRoomQueue = {
          employeeId: _.get(user, 'id'),
          oldDepartment: patientExam.department_virtual_code,
          newDepartment: patientExam.department_virtual_code,
          date: new Date(),
          patientCode: patientExam.patient_code,
          newDate: new Date(new Date().setDate(new Date().getDate() + 1)),
          reason,
        };
        const changeRoom = await regHttp.changePatientRegisterRoom(roomChange);
        const data = await configResponse(changeRoom);
        if (data) {
          setPrintStt({
            ...data,
            status: 'roomChange',
          });
        }
      } else {
        const roomChange: ChangeRoomQueue = {
          employeeId: _.get(user, 'id'),
          oldDepartment: patientExam.department_virtual_code,
          newDepartment: patientExam.department_virtual_code,
          date: new Date(),
          patientCode: patientExam.patient_code,
          newDate,
          reason,
        };
        const changeRoom = await regHttp.changePatientRegisterRoom(roomChange);
        const data = await configResponse(changeRoom);
        if (data) {
          setPrintStt({
            ...data,
            status: 'roomChange',
          });
        }
      }
      await notifyController
        .setNotifyMessage(
          `Thay i lch hn khm ca bnh nhn ${patientExam.full_name} thnh cng`,
        )
        .setSuccessNotify();
      await closeExam();
      await dispatch(
        QueueSlice.actions.getAllEndoscopic({ ...getFormValues() }),
      );
      dispatchModal({
        type: ModalType.SET_EXAM_LOAD,
        payload: false,
      });
    } catch (err) {
      notifyController.setNotifyMessage(_.get(err, 'message')).setFailNotify();
      dispatchModal({
        type: ModalType.SET_EXAM_LOAD,
        payload: false,
      });
    }
  };

  const closeExam = () => {
    dispatchModal({
      type: ModalType.OPEN_CHANGE_EXAM,
      payload: false,
    });
  };

  return (
    <div className="row">
      {loading && <AppLoading loading={true} />}
      <div className="d-none">
        <PrintSttEndo ref={printRef} info={printStt} />
      </div>
      {registerModal && (
        <RegisterEndoscopicModal
          open={registerModal}
          onClose={() => {
            dispatchModal({
              type: ModalType.OPEN_REGISTER,
              payload: false,
            });
          }}
        />
      )}
      {registerBoardingModal && (
        <RegisterBoardingModal
          open={registerBoardingModal}
          onClose={() => {
            dispatchModal({
              type: ModalType.OPEN_REGISTER_BOARDING,
              payload: false,
            });
          }}
        />
      )}
      {changeExamModal && (
        <ChangeRoomEndo
          open={changeExamModal}
          onClose={closeExam}
          loading={loadingExam}
          onChangeExam={infoPatient => onChangeExam(infoPatient)}
          appointmentDate={patientExam.appointment_date}
        />
      )}

      <div className="col-12">
        <div className="card card-topline-red">
          <div className="card-body">
            {renderTableControl()}
            <Paper className={classes.root}>
              <TableContainer className={classes.container}>
                <Table className={classes.table}>
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
          </div>
        </div>
      </div>
    </div>
  );
}
