/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useReducer } from 'react';
import * as _ from 'lodash';
import { RegisterHttp } from 'store/register/services/register.http';
import { DepartmentHttp } from 'store/department/services/department.http';
import { configResponse } from 'utils/request';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { NumberCall } from 'app/containers/STTPage/pages/CoordinationCenter';
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import styled from 'styled-components';
import moment from 'moment';
import useCountDown from 'react-countdown-hook';

const initialTime = 10000;
const interval = 1000;

const CallContainer = styled.div`
  padding: 28px;
  position: relative;
`;

const CallSwitch = styled.div`
  z-index: 10;
  width: 60px;
  font-size: 12px;
  display: flex;
  align-items: center;
`;

const CallTitle = styled.div`
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  line-height: 21px;
  color: var(--default-color);
  display: flex;
  border-bottom: 1px solid #ccc;
  > h3 {
    border: none;
    padding: 20px;
  }
`;

type Props = {
  dept: any;
  number: NumberCall;
};

interface CallState {
  active: boolean;
  normalNumber: number;
  priNumber: number;
  loading: boolean;
}

type Action = {
  type: string;
  payload: any;
};

const initState: CallState = {
  active: false,
  normalNumber: 0,
  priNumber: 0,
  loading: false,
};

function reduceState(state: CallState = initState, action: Action): CallState {
  const { type, payload } = action;
  switch (type) {
    case 'LOADING': {
      return {
        ...state,
        loading: action.payload,
      };
    }
    case 'ACTIVE':
      return {
        ...state,
        active: payload,
      };
    case 'NUMBER':
      const { normal_number, priority_number } = payload;
      return {
        ...state,
        normalNumber: normal_number,
        priNumber: priority_number,
      };
    default:
      return state;
  }
}

export function DisableEachCall({ dept, number }: Props) {
  const { name, id } = dept;
  const registerHttp = new RegisterHttp();
  const deptHttp = new DepartmentHttp();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const [{ active, normalNumber, priNumber, loading }, dispatch] = useReducer(
    reduceState,
    initState,
  );
  const [timeLeft, { start }] = useCountDown(initialTime, interval);

  useEffect(() => {
    if (!_.isEmpty(dept)) {
      getNumber(dept);
      dispatch({
        type: 'ACTIVE',
        payload: dept.is_active,
      });
    }
  }, [dept]);

  const getNumber = async (dept: any) => {
    try {
      const res = await registerHttp.registerQueueRoom({
        date: moment().format('YYYY-MM-DD'),
        room: dept.code,
      });
      const payload = await configResponse(res);
      await dispatch({
        type: 'NUMBER',
        payload,
      });
    } catch (err) {
      notifyController
        .setNotifyMessage(
          err
            ? _.get(new Error(err as any), 'message')
            : 'Ly s th t tht bi',
        )
        .setFailNotify();
    }
  };

  const onSwitch = async ({ id, is_active }) => {
    try {
      dispatch({
        type: 'LOADING',
        payload: true,
      });
      const res = await deptHttp.changeActive({ id, is_active });
      await configResponse(res);
      await dispatch({
        type: 'ACTIVE',
        payload: is_active,
      });
      dispatch({
        type: 'LOADING',
        payload: false,
      });
      notifyController
        .setNotifyMessage('Thay i trng thi bn thnh cng')
        .setSuccessNotify();
    } catch (err) {
      dispatch({
        type: 'LOADING',
        payload: false,
      });
      notifyController
        .setNotifyMessage(
          err
            ? _.get(new Error(err as any), 'message')
            : 'Thay i trng thi bn tht bi',
        )
        .setFailNotify();
    }
  };

  const callNumber = async (type: number) => {
    start();
    try {
      await registerHttp.registerQueueCall({
        room: dept.code,
        type,
        number: !type ? number.normal : number.priority,
      });
      await getNumber(dept);
    } catch (err) {}
  };

  return (
    <div className="col-md-4 mt-5">
      <div className="rooms text-center">
        <CallTitle className="room__title">
          <h3>{name}</h3>
          <CallSwitch>
            <Grid component="label">
              <Grid item>Bt/Tt</Grid>
              <Grid item>
                <Switch
                  checked={active}
                  disabled={Boolean(timeLeft)}
                  onChange={({
                    target: { checked },
                  }: React.ChangeEvent<HTMLInputElement>) =>
                    onSwitch({ id, is_active: checked })
                  }
                  color="primary"
                  name="changeAtive"
                  inputProps={{ 'aria-label': 'primary checkbox' }}
                />
              </Grid>
            </Grid>
          </CallSwitch>
        </CallTitle>
        <CallContainer>
          <div className="row">
            <div className="col-md-6">
              <div className="room__normal">
                <h4 className="room__title">S THNG</h4>
                <p className="room__number">{normalNumber}</p>
                <button
                  disabled={timeLeft || !active || loading ? true : false}
                  style={{
                    cursor: timeLeft || !active ? 'default' : 'pointer',
                  }}
                  className="btn btn__call"
                  onClick={() => callNumber(0)}
                >
                  GI {`(${!timeLeft ? initialTime / 1000 : timeLeft / 1000})`}
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="room__priority">
                <h4 className="room__title priority__color">S U TIN</h4>
                <p className="room__number priority__color">{priNumber}</p>
                <button
                  disabled={timeLeft || !active ? true : false}
                  style={{
                    cursor: timeLeft || !active ? 'default' : 'pointer',
                  }}
                  className="btn btn__call bg__red"
                  onClick={() => callNumber(1)}
                >
                  GI {`(${!timeLeft ? initialTime / 1000 : timeLeft / 1000})`}
                </button>
              </div>
            </div>
          </div>
        </CallContainer>
      </div>
    </div>
  );
}
