import React, { useState } from 'react';
import * as RoomSelector from 'store/room/shared/selectors';
import * as _ from 'lodash';
import { useSelector } from 'react-redux';
import { Dialog, DialogContent } from '@material-ui/core';
import { useForm } from 'react-hook-form';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import {
  CssTextField,
  useStylesDatepicker,
} from 'store/room/constants/http.constant';
import { AppHelper } from 'utils/app.helper';
import Autocomplete from '@material-ui/lab/Autocomplete';
import DateFnsUtils from '@date-io/date-fns';

type Props = {
  open: boolean;
  onClose: () => void;
  onChangeRoom: (id) => void;
};

export function ModalChangeRoom({ open, onClose, onChangeRoom }: Props) {
  const dataRoomAll: any = useSelector<any>(RoomSelector.selectRoomCombine);
  const { register, errors, setError, clearErrors, handleSubmit } = useForm();
  const [room, setRoom] = useState<any>({});
  const [newDate, setNewDate] = useState<any>(AppHelper.parseBODDate());
  const datepickerClass = useStylesDatepicker();

  const changeRoom = room => {
    setRoom(room);
    if (!_.isEmpty(room)) {
      clearErrors(['room']);
    }
  };

  const submit = ({ reason }: any): void => {
    if (_.isEmpty(room)) {
      return setError('room', { type: 'required' });
    }
    onChangeRoom({
      newRoom: room,
      reason,
      newDate,
    });
  };

  return (
    <Dialog
      fullWidth={true}
      maxWidth={'xs'}
      open={open}
      onClose={onClose}
      disableBackdropClick={true}
      scroll={'body'}
      aria-labelledby="scroll-dialog-title"
      aria-describedby="scroll-dialog-description"
    >
      <DialogContent>
        <form onSubmit={handleSubmit(submit)}>
          <h3 className="modal__title px-0">I PHNG KHM</h3>
          <div className="w-100 mb-2">
            <h5 className="modal__title pl-0">Bn khm</h5>
            <Autocomplete
              options={
                !_.isEmpty(dataRoomAll) && dataRoomAll.length ? dataRoomAll : []
              }
              getOptionLabel={(option: any) =>
                option.description + ' - ' + option.code
              }
              renderInput={(params: any) => {
                return (
                  <CssTextField
                    inputRef={register({ required: true })}
                    style={{ background: 'white', fontSize: '16px' }}
                    {...params}
                    variant="outlined"
                    name="room"
                    placeholder="Nhp tn phng khm"
                  />
                );
              }}
              onChange={(event, value) => changeRoom(value)}
            />
            {errors.room && errors.room?.type === 'required' && (
              <span className="d-block" style={{ color: ' #fb0606' }}>
                Vui lng chn phng khm
              </span>
            )}
          </div>
          <div className="w-100 mb-2">
            <div className="form-group text-edit">
              <h5 className="modal__title pl-0">Ngy</h5>
              <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <KeyboardDatePicker
                  variant="inline"
                  className={datepickerClass.textField}
                  autoOk={true}
                  placeholder="Nhp ngy thng nm - ex: 01/01/2021"
                  inputVariant="outlined"
                  invalidDateMessage="Vui lng nhp ng nh dng ngy thng"
                  maxDateMessage="Sai nh dng nm hoc nm vt qu gii hn 2100"
                  format="dd/MM/yyyy"
                  margin="normal"
                  minDate={new Date()}
                  value={newDate}
                  onChange={(date: any) => setNewDate(date)}
                />
              </MuiPickersUtilsProvider>
            </div>
          </div>
          <div className="w-100 mb-2">
            <h5 className="modal__title pl-0">L do</h5>
            <textarea
              className="form-control"
              rows={3}
              name="reason"
              ref={register({ required: true })}
            />
            {errors.reason && errors.reason?.type === 'required' && (
              <span className="d-block" style={{ color: ' #fb0606' }}>
                Vui lng nhp l do
              </span>
            )}
          </div>
          <div className="modal__btn d-flex justify-content-end mt-4 pr-0">
            <button
              type="button"
              className="btn btn-secondary btn__cancel mr-2"
              onClick={onClose}
            >
              HY
            </button>
            <button
              type="submit"
              className="btn btn__default--bg-color btn__create"
            >
              I
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
