/* eslint-disable no-unreachable */
/* eslint-disable prettier/prettier */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState, useRef } from 'react';
import * as RoomSlice from 'store/room/shared/slice';
import * as RoomSelector from 'store/room/shared/selectors';
import * as RegisterSlice from 'store/register/shared/slice';
import * as RegisterSelector from 'store/register/shared/selectors';
import * as _ from 'lodash';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { RoomSaga } from 'store/room/shared/saga';
import { RegisterSaga } from 'store/register/shared/saga';
import { useSelector, useDispatch } from 'react-redux';
import { DisableEachCall } from '../components/DisableEachCall';
import { TIMER_RECALL } from 'store/register/constants/register.constant';
import { QueueEyeAll } from 'store/register/services/register.http';
import { UltraSoundCounter as Counter } from 'app/components/UltraSoundCounter';
import { CssTextField } from 'store/room/constants/http.constant';
import { useForm } from 'react-hook-form';
import {
  LocalStorageService,
  LocalStorageKey,
} from 'services/localStorage.service';
import { TEKDateTimeService } from 'services/datetime.service';
import { TypePatient } from 'store/patient/constants/patient.constant';
import { RouteCategory } from 'router';
import AppLoading from 'app/components/Loading';
import logo from 'img/logo-removebg-preview.png';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { ConsoleSqlOutlined } from '@ant-design/icons';

export function UltraSoundPage() {
  const localStorage = new LocalStorageService();
  useInjectReducer({ key: RoomSlice.sliceKey, reducer: RoomSlice.reducer });
  useInjectSaga({ key: RoomSlice.sliceKey, saga: RoomSaga });
  useInjectReducer({
    key: RegisterSlice.sliceKey,
    reducer: RegisterSlice.reducer,
  });
  useInjectSaga({ key: RegisterSlice.sliceKey, saga: RegisterSaga });
  const dispatch = useDispatch();
  const { register, errors, clearErrors } = useForm();
  const roomStore: any = useSelector<any>(RoomSelector.selectRoomsUltraSound);
  const loading: any = useSelector<any>(RoomSelector.selectLoading);
  const eyesList: any = useSelector<any>(
    RegisterSelector.selectUltraSoundLists,
  );
  console.log('eyesList', eyesList);
  const [tableProps, setTableProps] = useState<any[]>([]);
  console.log('tableProps', tableProps);
  const timerRef = useRef<any>(null);
  const [normal, setNormal] = useState<number>(1);
  const [priority, setPriority] = useState<number>(1);
  const [isCall, setIsCall] = useState<boolean>(false);
  const [room, setRoom] = useState<any[]>([]);
  console.log(123, room);
  const [save, setSave] = useState<boolean>(true);

  useEffect(() => {
    function loadRoomStorage() {
      const roomCache: any = localStorage.getItem(LocalStorageKey.ultraSound);
      const formatCache: any[] = JSON.parse(roomCache);
      if (!_.isEmpty(formatCache) && formatCache.length) {
        setRoom(formatCache);
        onCall(formatCache);
      }
    }
    dispatch(RoomSlice.actions.getRoomUltraSound());
    loadRoomStorage();
    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
      }
    };
  }, []);

  useEffect(() => {
    function reduceProps(listEye, listTable) {
      if (_.isEmpty(listEye) || _.isEmpty(listTable)) return;
      if (listEye.length !== listTable.length) return;
      const dataProps = listEye.map((tab, idx) => {
        const { code, description } = listTable[idx];
        return {
          ...tab,
          code,
          description,
        };
      });
      setTableProps(dataProps);
    }
    reduceProps(eyesList, room);
  }, [eyesList, room]);

  const callRoomNumber = rooms => {
    dispatch(
      RegisterSlice.actions.getAllUltraSound(
        rooms.map(
          ({ code }): QueueEyeAll => ({
            department_code: code,
            limit: 5,
            date: TEKDateTimeService.formatOutTime(new Date()),
          }),
        ),
      ),
    );
  };

  const onCall = rooms => {
    if (!rooms.length) return;
    if (timerRef.current) {
      clearInterval(timerRef.current);
    }
    callRoomNumber(rooms);
    timerRef.current = setInterval(
      callRoomNumber.bind(callRoomNumber, rooms),
      TIMER_RECALL,
    );
    setIsCall(true);
  };

  const changeRoom = room => {
    setRoom(_.sortBy(room, 'code'));
    if (!_.isEmpty(room)) {
      clearErrors(['room']);
    }
  };

  const handleClick = () => {
    if (room.length) {
      onCall(room);
      if (save) {
        localStorage.setItem({
          key: LocalStorageKey.ultraSound,
          value: JSON.stringify(room),
        });
      }
    }
  };

  const stopCall = () => {
    setIsCall(false);
    localStorage.removeItem(LocalStorageKey.ultraSound);
    setRoom([]);
  };

  return (
    <React.Fragment>
      <div id="home" className="container-fluid">
        {loading ? <AppLoading loading={true} /> : null}
        {isCall && (
          <div className="btn-cancel f-rigth">
            <button className="btn btn__default--bg-color" onClick={stopCall}>
              kt thc
            </button>
          </div>
        )}
        {!isCall ? (
          <div className="limiter1">
            <div className="container-login101 container">
              <div className="row">
                <div className="col-md-12">
                  <div className="form-group text-edit">
                    <label>BN NHN BNH</label>
                    <Autocomplete
                      options={
                        !_.isEmpty(roomStore) && roomStore.length
                          ? roomStore
                          : []
                      }
                      value={room}
                      multiple
                      limitTags={2}
                      style={{ height: '39px' }}
                      getOptionLabel={(option: any) => option.description}
                      renderInput={(params: any) => {
                        return (
                          <CssTextField
                            inputRef={register({ required: true })}
                            style={{ background: 'white', fontSize: '16px' }}
                            {...params}
                            variant="outlined"
                            name="room"
                            placeholder="Nhp tn phng khm"
                          />
                        );
                      }}
                      onChange={(event, value) => changeRoom(value)}
                    />
                    {errors.room && errors.room?.type === 'required' && (
                      <span
                        className="d-block"
                        style={{
                          color: ' #fb0606',
                          transform: 'translateY(20px)',
                        }}
                      >
                        Vui lng chn phng khm
                      </span>
                    )}
                  </div>
                </div>
                <div className="col-md-12" style={{ marginTop: '7.5rem' }}>
                  <div
                    className="form-check float-right text-edit mt-3"
                    style={{ zIndex: 2 }}
                  >
                    <input
                      className="form-check-input"
                      type="checkbox"
                      checked={save}
                      onChange={e => setSave(e.target.checked)}
                      style={{ marginTop: '.5rem' }}
                    />
                    <label
                      className="form-check-label ml-1"
                      style={{ fontWeight: 400 }}
                    >
                      Lu ci t
                    </label>
                  </div>
                  <div>
                    <div className="container-login101-form-btn">
                      <div className="wrap-login101-form-btn">
                        <div className="login101-form-bgbtn"></div>
                        <button
                          className="login101-form-btn"
                          style={{ background: 'red' }}
                          onClick={handleClick}
                        >
                          BT U
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          <>
            {isCall && (
              <div>
                {room.length ? (
                  <>
                    <div className="container-fluid">
                      <div className="row justify-content-lg-center">
                        {tableProps.map((table, idx) => {
                          return (
                            <DisableEachCall
                              key={idx}
                              limit={{ normal, priority }}
                              onCall={() => onCall(room)}
                              {...table}
                            />
                          );
                        })}
                      </div>
                    </div>

                    <div className="row my-4">
                      <div className="col-md-4">
                        <p className="font-weight-bold text-uppercase">
                          S lng bnh nhn
                        </p>
                      </div>
                      <div className="col-md-4 d-flex align-items-center">
                        <p className="room__title mr-3">S THNG</p>
                        <Counter
                          count={normal}
                          config={TypePatient.NORMAL}
                          setAsc={() => {
                            normal < 10 ? setNormal(normal + 1) : setNormal(10);
                          }}
                          setDesc={() => {
                            normal >= 2 ? setNormal(normal - 1) : setNormal(1);
                          }}
                        />
                      </div>
                      <div className="col-md-4 d-flex align-items-center">
                        <p className="room__title mr-3 priority__color">
                          S U TIN
                        </p>
                        <Counter
                          count={priority}
                          config={TypePatient.PRIORITY}
                          setAsc={() => {
                            priority < 10
                              ? setPriority(priority + 1)
                              : setPriority(10);
                          }}
                          setDesc={() => {
                            priority >= 2
                              ? setPriority(priority - 1)
                              : setPriority(1);
                          }}
                        />
                      </div>
                    </div>
                  </>
                ) : (
                  <AppLoading loading={true} />
                )}
              </div>
            )}
          </>
        )}
      </div>
    </React.Fragment>
  );
}
