/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from 'react';
import { RegisterHttp, Queue } from 'store/register/services/register.http';
import * as _ from 'lodash';

interface Limit {
  normal: number;
  priority: number;
}

type Props = {
  id: string;
  code: string;
  description: string;
  normal: number;
  priority: number;
  limit: Limit;
  onCall: () => void;
  handleDeleteTable?: (id: string) => void;
  setDisabledNormal?: any;
  onDisabledNormal?: boolean;
  onDisabledPrio?: boolean;
  setDisablePrio?: any;
  secondNormal?: number;
  secondPriority?: number;
};

interface ICall {
  disabled: boolean;
  number: number;
}

export function DisableEachCall({
  code,
  limit,
  description,
  onCall,
  secondNormal,
  secondPriority,
  ...props
}: Props) {
  const http = new RegisterHttp();
  const [resultNormal, setResultNormal] = useState<any>(0);
  const [resultPriority, setResultPriority] = useState<any>(0);
  const [isCall, setIsCall] = useState<ICall>({
    disabled: false,
    number: 10,
  });

  useEffect(() => {
    function configInterval() {
      if (isCall.disabled) {
        return setInterval(() => {
          if (isCall.number >= 1) {
            setIsCall({
              ...isCall,
              number: isCall.number - 1,
            });
          }
          if (isCall.number < 1) {
            setIsCall({
              disabled: false,
              number: 10,
            });
          }
        }, 1000);
      }
    }
    let loopDisabled = configInterval();
    return () => {
      clearInterval(loopDisabled);
    };
  }, [isCall]);

  useEffect(() => {
    if (!_.isEmpty(props)) {
      setResultNormal(_.get(props, 'normal_number'));
      setResultPriority(_.get(props, 'priority_number'));
    }
  }, [props]);

  const callEyeNumber = (type: number) => {
    setIsCall({
      ...isCall,
      disabled: true,
    });
    const eyesQueue: Queue = {
      room: code,
      number: type ? limit.priority : limit.normal,
      type,
    };

    http
      .multiCastGetQueueRoom(eyesQueue)
      .then(response => response.data)
      .then(response => {
        const { normal_number, priority_number } = _.get(response, 'result');
        setResultPriority(priority_number);
        setResultNormal(normal_number);
        onCall();
      });
  };

  return (
    <div className="col-md-4 mt-5">
      <div className="rooms text-center">
        <a className="delete__room" hidden>
          <i className="material-icons">clear</i>
        </a>
        <div className="room__title">
          <h3>{description} </h3>
        </div>
        <div className="room__container">
          <div className="row">
            <div className="col-md-6">
              <div className="room__normal">
                <h4 className="room__title">S THNG</h4>
                <p className="room__number">{resultNormal}</p>
                <p className="room__text">
                  Bnh nhn ch: {_.get(props, 'number_of_normal_patient') || 0}
                </p>
                <button
                  className="btn btn__call"
                  onClick={() => callEyeNumber(0)}
                  disabled={isCall.disabled}
                >
                  GI {isCall.disabled ? `(${isCall.number})` : null}
                </button>
              </div>
            </div>
            <div className="col-md-6">
              <div className="room__priority">
                <h4 className="room__title priority__color">S U TIN</h4>
                <p className="room__number priority__color">{resultPriority}</p>
                <p className="room__text">
                  Bnh nhn ch:{' '}
                  {_.get(props, 'number_of_priority_patient') || 0}
                </p>
                <button
                  className="btn btn__call bg__red"
                  onClick={() => callEyeNumber(1)}
                  disabled={isCall.disabled}
                >
                  GI {isCall.disabled ? `(${isCall.number})` : null}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
