/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import * as LocationSelectors from 'store/location/shared/selectors';
import * as QueueSelectors from 'store/queue/shared/selectors';
import * as QueueSlice from 'store/queue/shared/slice';
import * as DeptGroupSelector from 'store/departmentGroup/shared/selectors';
import * as _ from 'lodash';
import { useSelector, useDispatch } from 'react-redux';
import { QueueSaga } from 'store/queue/shared/saga';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { DEFAULT_FORMAT_INPUT_DATE } from 'store/common/constants/common.constant';
import { AppHelper } from 'utils/app.helper';
import { makeStyles } from '@material-ui/core';
import { PatientHttp } from 'store/patient/services/patient.http';
import { useForm } from 'react-hook-form';
import {
  RESPONSE_CONSTANT,
  RESPONSE_MESSAGE,
} from 'store/queue/constants/queue.constant';
import AppLoading from 'app/components/Loading';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DateFnsUtils from '@date-io/date-fns';
import Radio from '@material-ui/core/Radio';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';

const useStyles = makeStyles({
  root: {
    '& legend': {
      fontSize: '14px !important',
      color: '#000',
      fontWeight: 'bold',
    },
  },
});

const defaultUser = {
  wardId: '',
  provinceId: '',
  groupCode: '',
  distId: '',
  patientType: '',
  name: '',
  gender: 0,
  firstName: '',
  lastName: '',
  fullName: '',
  street: '',
  formatBirthDay: '',
};

class PatientRegister {
  code: string = '';
  wardId: string = '';
  provinceId: string = '';
  distId: string = '';
  wardName: string = '';
  provinceName: string = '';
  distName: string = '';
  gender: number = 0;
  firstName: string = '';
  lastName: string = '';
  fullName: string = '';
  birthDay: string = '';
  street: string = '';
  constructor(userRes: any) {
    this.code = userRes.code ? userRes.code : '';
    this.wardId = userRes.ward_id ? userRes.ward_id : '';
    this.wardName = userRes.ward_name;
    this.provinceId = userRes.province_id ? userRes.province_id : '';
    this.provinceName = userRes.province_name;
    this.distId = userRes.district_id ? userRes.district_id : '';
    this.distName = userRes.district_name;
    this.gender = userRes.gender_code ? +userRes.gender_code : 0;
    this.firstName = userRes.first_name;
    this.lastName = userRes.last_name;
    this.fullName = userRes.full_name;
    this.birthDay = userRes.birthday;
    this.street = userRes.street;
  }
}

export function RegisterEndoscopicModal({ open, onClose }) {
  const classes = useStyles();
  const dispatch = useDispatch();
  const patientHttp = new PatientHttp();
  useInjectReducer({
    key: QueueSlice.sliceKey,
    reducer: QueueSlice.reducer,
  });
  useInjectSaga({ key: QueueSlice.sliceKey, saga: QueueSaga });
  const registerDay = new Date();
  const locations: any[] = useSelector(LocationSelectors.selectLocations);
  const success: any = useSelector<any>(QueueSelectors.selectSuccess);
  const groups: any = useSelector(DeptGroupSelector.selectGroups);
  const [patientCode, setPatientCode] = useState<string>('');
  const [loading, setLoading] = useState<boolean>(false);
  const { register, errors, handleSubmit, clearErrors } = useForm();
  const [user, setUser] = useState<any>(defaultUser);
  const [dists, setDists] = useState<any[]>([]);
  const [wards, setWards] = useState<any[]>([]);
  const dateUtil = new DateFnsUtils();

  useEffect(() => {
    if (user.provinceId !== '') {
      const locationSelect = locations.find(
        loc => loc.province_code === user.provinceId,
      );
      if (locationSelect) {
        let dists = _.sortBy(locationSelect.districts, [
          'district_name',
          'district_code',
        ]);
        setDists(dists);
        if (user.distId !== '') {
          const dist = dists.find(dist => dist.district_code === user.distId);
          if (dist) {
            let wards = _.sortBy(dist.wards, ['ward_name', 'ward_code']);
            setWards(wards);
          }
        }
      }
    }
  }, [user]);

  useEffect(() => {
    if (!_.isEmpty(success)) {
      switch (success.key) {
        case RESPONSE_CONSTANT.REGISTER_ENDOSCOPIC_SUCCESS:
          onClose();
          break;
        default:
          break;
      }
    }
  }, [success]);

  const changePatientCode = e => setPatientCode(e.target.value);

  const changeProvince = e => {
    setDists([]);
    setWards([]);
    setUser({
      ...user,
      provinceId: e.target.value,
    });
  };

  const changeDist = e => {
    const distId = e.target.value;
    setUser({
      ...user,
      distId,
    });
    const distSelect = dists.find(dist => dist.district_code === distId);
    if (distSelect) {
      let wards = _.sortBy(distSelect.wards, ['ward_name', 'ward_code']);
      setWards(wards);
    }
  };

  const changeWard = e => {
    setUser({
      ...user,
      wardId: e.target.value,
    });
  };

  const searchPatient = () => {
    if (patientCode === '') {
      dispatch(
        QueueSlice.actions.setError({
          id: AppHelper.generateUUID() + Date.now(),
          key: RESPONSE_CONSTANT.REQUIRED_PATIENT_CODE,
          message: RESPONSE_MESSAGE.REQUIRED_PATIENT_CODE,
        }),
      );
      return;
    }
    setLoading(true);
    patientHttp
      .getPatientWithType({
        patientCode,
        requestDate: new Date(),
      })
      .then(response => response.data)
      .then(responseData => {
        let userFormat: any = {};
        if (AppHelper.checkResponseData(responseData)) {
          dispatch(
            QueueSlice.actions.setSuccess({
              id: AppHelper.generateUUID() + Date.now(),
              key: RESPONSE_CONSTANT.FIND_PATIENT_SUCCESS,
              message: RESPONSE_MESSAGE.FIND_PATIENT_SUCCESS,
            }),
          );
          userFormat = new PatientRegister(responseData.result);
          clearErrors();
        } else {
          dispatch(
            QueueSlice.actions.setError({
              id: AppHelper.generateUUID() + Date.now(),
              key: RESPONSE_CONSTANT.FIND_PATIENT_FAIL,
              message: responseData.message,
            }),
          );
          userFormat = _.clone(defaultUser);
        }
        setUser(userFormat);
        setLoading(false);
      })
      .catch(error => {
        setUser(_.clone(defaultUser));
        setLoading(false);
      });
  };

  const changeGroupCode = e => {
    setUser({
      ...user,
      groupCode: e.target.value,
    });
  };

  const changeStreet = e => {
    setUser({
      ...user,
      street: e.target.value,
    });
  };

  const changeBirthDay = e => {
    setUser({
      ...user,
      birthDay: e.target.value,
    });
  };

  const changeFullName = e => {
    setUser({
      ...user,
      fullName: e.target.value,
    });
  };

  const changeGender = event => {
    setUser({
      ...user,
      gender: +event.target.value,
    });
  };

  const submitRegister = (info: any) => {
    const { firstName, lastName } = AppHelper.getFirstLastName(user.fullName);
    const registerInfo = {
      hospital_code: '',
      group_code: user.groupCode,
      patient_code: info.patientCode,
      first_name: firstName ? firstName : '',
      last_name: lastName ? lastName : '',
      birthday: info.birthDay,
      gender: user.gender,
      province_id: info.provinceId,
      district_id: info.distId,
      ward_id: info.wardId,
      street: info.street,
      phone: '',
      registered_date: registerDay,
    };
    dispatch(QueueSlice.actions.registerEndoscopic(registerInfo));
  };

  return (
    <Dialog
      disableBackdropClick={true}
      fullWidth={true}
      maxWidth={'md'}
      open={open}
      onClose={onClose}
    >
      <DialogContent>
        {loading && <AppLoading loading={true} />}
        <form onSubmit={handleSubmit(submitRegister)}>
          <h4 className="modal-title mb-4">ng K S Th T Khu Ni Soi</h4>
          <div className="row">
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="code">
                M bnh nhn
              </label>
              <div className="input-group">
                <input
                  id="patientCode"
                  type="text"
                  className="form-control"
                  name="patientCode"
                  placeholder="Nhp M bnh nhn"
                  value={patientCode}
                  onChange={changePatientCode}
                  ref={register({ required: true })}
                />
                <div className="input-group-append">
                  <button
                    className="btn btn-primary m-0"
                    type="button"
                    onClick={searchPatient}
                  >
                    Tm bnh nhn
                  </button>
                </div>
              </div>
              {errors.patientCode &&
                errors.patientCode?.type === 'required' && (
                  <span style={{ color: ' #fb0606' }}>
                    Vui lng chn m bnh nhn
                  </span>
                )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="control-label text-edit pl-0 font-weight-bold">
                Nhm dch v
              </label>
              <select
                name="room"
                className="form-control"
                value={user.groupCode}
                onChange={changeGroupCode}
                ref={register({ required: true })}
              >
                <option value="">Nhp tn Nhm dch v</option>
                {!_.isEmpty(groups.data)
                  ? groups.data.map((row, idx) => (
                      <option value={row.code} key={idx}>
                        {row.description + ' - ' + row.code}
                      </option>
                    ))
                  : null}
              </select>
              {errors.room && errors.room?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn Nhm phng khm
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="fullName">
                Tn bnh nhn
              </label>
              <input
                id="fullName"
                type="text"
                className="form-control"
                name="fullName"
                placeholder="Nhp Tn bnh nhn"
                value={user.fullName}
                onChange={changeFullName}
                ref={register({ required: true })}
              />
              {errors.fullName?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng nhp h v tn
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1 pt-3">
              <FormControl component="fieldset" className={classes.root}>
                <FormLabel component="legend">Gii tnh</FormLabel>
                <div>
                  <FormControlLabel
                    control={
                      <Radio
                        checked={!user.gender ? true : false}
                        color="primary"
                        onChange={changeGender}
                        value="0"
                        name="gender"
                      />
                    }
                    label="N"
                  />
                  <FormControlLabel
                    control={
                      <Radio
                        checked={user.gender ? true : false}
                        color="primary"
                        onChange={changeGender}
                        value="1"
                        name="gender"
                      />
                    }
                    label="Nam"
                  />
                </div>
              </FormControl>
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="provinceId">
                Tnh/Thnh ph
              </label>
              <select
                name="provinceId"
                className="form-control"
                value={user.provinceId}
                onChange={changeProvince}
                ref={register({ required: true })}
              >
                <option value="">Tnh/Thnh ph</option>
                {locations.map((province, idx) => (
                  <option value={province.province_code} key={idx}>
                    {province.province_name}
                  </option>
                ))}
              </select>
              {errors.provinceId && errors.provinceId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn tnh/thnh ph
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="distId">
                Qun/Huyn
              </label>
              <select
                id="distId"
                name="distId"
                className="form-control"
                value={user.distId}
                onChange={changeDist}
                ref={register({ required: true })}
              >
                <option value="">Chn qun</option>
                {dists.length
                  ? dists.map((dist: any) => {
                      return (
                        <option
                          key={dist.district_code}
                          value={dist.district_code}
                        >
                          {dist.district_name}
                        </option>
                      );
                    })
                  : null}
              </select>
              {errors.distId && errors.distId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>Vui lng chn qun</span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="wardId">
                Phng/X
              </label>
              <select
                name="wardId"
                className="form-control"
                placeholder="Phng/X"
                value={user.wardId}
                onChange={changeWard}
                ref={register({ required: true })}
              >
                <option value="">Chn phng x</option>
                {wards?.length
                  ? wards.map((ward: any) => {
                      return (
                        <option value={ward.ward_code} key={ward.ward_code}>
                          {ward.ward_name}
                        </option>
                      );
                    })
                  : null}
              </select>
              {errors.wardId && errors.wardId?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn phng x
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="street">
                ng/Thn/p
              </label>
              <input
                type="text"
                className="form-control"
                name="street"
                placeholder="ng/Thn/p"
                value={user.street}
                onChange={changeStreet}
                ref={register()}
              />
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="birthDay">
                Ngy sinh
              </label>
              <input
                id="birthDay"
                name="birthDay"
                type="date"
                className="form-control"
                placeholder="Ngy sinh"
                ref={register({ required: true })}
                max={dateUtil.format(new Date(), DEFAULT_FORMAT_INPUT_DATE)}
                value={
                  !_.isEmpty(user.birthDay)
                    ? dateUtil.format(
                        new Date(user.birthDay),
                        DEFAULT_FORMAT_INPUT_DATE,
                      )
                    : ''
                }
                onChange={changeBirthDay}
              />
              {errors.birthDay && errors.birthDay?.type === 'required' && (
                <span style={{ color: ' #fb0606' }}>
                  Vui lng chn ngy sinh
                </span>
              )}
            </div>
            <div className="form-group col-sm-12 col-md-6 mb-1">
              <label className="font-weight-bold" htmlFor="registerDay">
                Ngy ng k
              </label>
              <input
                id="registerDay"
                name="registerDay"
                type="date"
                className="form-control"
                placeholder="Ngy ng k"
                value={dateUtil.format(
                  new Date(registerDay),
                  DEFAULT_FORMAT_INPUT_DATE,
                )}
                disabled
              />
              {errors.registerDay &&
                errors.registerDay?.type === 'required' && (
                  <span style={{ color: ' #fb0606' }}>
                    Vui lng chn ngy ng k
                  </span>
                )}
            </div>
          </div>
          <div className="form-group d-flex justify-content-end mt-2">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              ng
            </button>
            <button type="submit" className="btn btn-primary ml-2">
              ng k
            </button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  );
}
