/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable prettier/prettier */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable jsx-a11y/heading-has-content */
import React from 'react';
import * as _ from 'lodash';
import { TEKDateTimeService } from 'services/datetime.service';
import { useSelector } from 'react-redux';
import { SettingModel } from 'store/common/shared/slice';
import * as fromCommon from 'store/common/shared/selectors';
import moment from 'moment';

const Barcode = require('react-barcode');

type Props = {
  info: any;
};

export const PrintSttEndo = React.forwardRef<HTMLDivElement, Props>(
  ({ info }, ref) => {
    const setting: SettingModel = useSelector(fromCommon.selectDefaultSetting);
    if (_.isEmpty(info)) return <></>;
    const {
      data: { number, department_name, type },
      code,
      last_name,
      first_name,
      gender,
      birthday,
      registered_date,
      status,
      patient_type,
    } = info;
    return (
      <div ref={ref} {...info}>
        <meta charSet="utf-8" />
        <title>{status === 'roomChange' ? 'GIY HN KHM' : 'S TH T'}</title>
        <style
          dangerouslySetInnerHTML={{
            __html:
              '\n\t\t* {\n\t\t\tmargin:  0;\n\t\t\tpadding: 0;\n\t\t\tfont-family: sans-serif;\n\t\t}\n\t',
          }}
        />
        <div style={{ width: '450px', padding: '0 30px' }}>
          <header
            style={{
              display: 'flex',
              justifyContent: 'center',
            }}
          >
            <img src={setting.logoUrl} width={90} />
            <div style={{ padding: '5px' }}>
              <h5>CHO RAY HOSPITAL</h5>
              <h3 style={{ fontSize: '24px', fontWeight: 'bold' }}>
                BNH VIN CH RY
              </h3>
            </div>
          </header>
          <div
            style={{
              padding: '10px',
              textAlign: 'center',
            }}
          >
            <h3 style={{ fontSize: '30px', fontWeight: 'bold' }}>
              {status === 'roomChange' ? 'GIY HN KHM' : 'S TH T'}
            </h3>
            <p style={{ fontWeight: 'bold' }}>
              {TEKDateTimeService.getCurrentTime()} Ngy {moment().format('DD')}{' '}
              thng {moment().format('MM')} nm {moment().format('YYYY')}
            </p>
            <p>
              <Barcode
                value={code}
                width={2}
                displayValue={false}
                height={50}
                fontSize={16}
                format={'CODE128'}
                textAlign={'center'}
                textPosition={'bottom'}
                background={'#ffffff'}
                lineColor={'#000000'}
                margin={2}
              />
            </p>
            <p />
          </div>
          <div>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '45%' }}>
                M Cp Cu / M BN:
              </span>
              <span style={{ display: 'block', fontWeight: 'bold' }}>
                {code}
              </span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '20%' }}>Tn BN: </span>
              <span style={{ display: 'block', fontWeight: 'bold', flex: 1 }}>
                {last_name} {first_name}
              </span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '20%' }}>Gii tnh </span>
              <span
                style={{ display: 'block', fontWeight: 'bold', flex: '0.3' }}
              >
                {gender}
              </span>
              <span style={{ display: 'block', width: '90px' }}>
                Nm sinh:{' '}
              </span>
              <span style={{ display: 'block', fontWeight: 'bold', flex: 1 }}>
                {birthday.slice(0, 4)}
              </span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '20%' }}>Bc s: </span>
              <span style={{ display: 'block', fontWeight: 'bold' }}></span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '40%' }}>
                Chuyn Khoa:{' '}
              </span>
              <span style={{ display: 'block', fontWeight: 'bold' }}></span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '35%' }}>
                Loi hnh khm:
              </span>
              <span style={{ display: 'block', fontWeight: 'bold' }}>
                THU PH
              </span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '20%' }}>Th BHYT: </span>
              <span style={{ display: 'block', fontWeight: 'bold' }}></span>
            </p>
            <p
              style={{
                fontSize: '16px',
                display: 'flex',
              }}
            >
              <span style={{ display: 'block', width: '40%' }}>
                Phng khm:{' '}
              </span>
            </p>
            <h3
              style={{
                width: '100%',
                textAlign: 'center',
                fontWeight: 'bold',
                borderBottom: '2px solid #888',
              }}
            >
              {department_name}
            </h3>
          </div>
          <div>
            <span
              style={{
                fontSize: '20px',
                marginTop: '5px',
              }}
            >
              S th t:
            </span>
            <div style={{ textAlign: 'center', marginBottom: '20px' }}>
              <p
                style={{
                  fontSize: '50px',
                  fontWeight: 'bold',
                }}
              >
                {number > 0 ? number : '-'}
              </p>
              {!_.isEmpty(patient_type) && patient_type !== 'Thng' ? (
                <span
                  style={{
                    boxSizing: 'border-box',
                    border: '2px solid #000',
                    padding: '10px',
                    fontSize: '20px',
                    fontWeight: 'bold',
                  }}
                >
                  {patient_type}
                </span>
              ) : !_.isEmpty(type) && type !== 'Thng' ? (
                <span
                  style={{
                    boxSizing: 'border-box',
                    border: '2px solid #000',
                    padding: '10px',
                    fontSize: '20px',
                    fontWeight: 'bold',
                  }}
                >
                  {type}
                </span>
              ) : (
                ''
              )}
            </div>
            <div style={{ textAlign: 'center' }}>
              <p
                style={{
                  fontWeight: 'bold',
                  fontSize: '20px',
                }}
              >
                {status === 'roomChange'
                  ? `NGY HN KHM: ${moment(registered_date).format(
                      'DD/MM/YYYY',
                    )}`
                  : `NGY THC HIN: ${moment().format('DD/MM/YYYY')}`}
              </p>
            </div>
            <footer
              style={{
                borderBottom: '2px dashed #000',
              }}
            >
              <p style={{ textAlign: 'center' }}>
                Bnh nhn vui lng gi phiu cho n khi kt thc khm ti bnh
                vin.
              </p>
            </footer>
          </div>
        </div>
      </div>
    );
  },
);
